import React, { useState, useEffect } from 'react';
import { Brain, Clock, Zap, Target, Calendar, TrendingUp, Lightbulb } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock, BlockCategory, Priority, EnergyLevel } from '../types';

interface AISuggestion {
  id: string;
  type: 'schedule' | 'optimize' | 'warning' | 'tip';
  title: string;
  description: string;
  action?: () => void;
  priority: 'low' | 'medium' | 'high';
}

const AIAutoScheduler: React.FC = () => {
  const { blocks, addBlock, selectedDate, user } = useStore();
  const [isGenerating, setIsGenerating] = useState(false);
  const [suggestions, setSuggestions] = useState<AISuggestion[]>([]);
  const [userPreferences, setUserPreferences] = useState({
    workStart: '09:00',
    workEnd: '17:00',
    breakDuration: 15,
    maxBlockDuration: 120,
    preferredEnergy: 'high' as EnergyLevel,
    focusCategories: ['work', 'study'] as BlockCategory[],
  });

  // AI-powered day analysis
  const analyzeDay = () => {
    const todayBlocks = blocks.filter(block => 
      block.scheduledFor && 
      new Date(block.scheduledFor).toDateString() === selectedDate.toDateString()
    );

    const analysis = {
      totalDuration: todayBlocks.reduce((sum, block) => sum + block.duration, 0),
      completedBlocks: todayBlocks.filter(block => block.completed).length,
      energyDistribution: {
        high: todayBlocks.filter(block => block.energyLevel === 'high').length,
        medium: todayBlocks.filter(block => block.energyLevel === 'medium').length,
        low: todayBlocks.filter(block => block.energyLevel === 'low').length,
      },
      categoryDistribution: {} as Record<BlockCategory, number>,
      timeDistribution: {
        morning: 0,
        afternoon: 0,
        evening: 0,
      }
    };

    // Analyze category distribution
    todayBlocks.forEach(block => {
      analysis.categoryDistribution[block.category] = 
        (analysis.categoryDistribution[block.category] || 0) + 1;
    });

    // Analyze time distribution
    todayBlocks.forEach(block => {
      if (block.scheduledFor) {
        const hour = new Date(block.scheduledFor).getHours();
        if (hour < 12) analysis.timeDistribution.morning += block.duration;
        else if (hour < 18) analysis.timeDistribution.afternoon += block.duration;
        else analysis.timeDistribution.evening += block.duration;
      }
    });

    return analysis;
  };

  // Generate AI suggestions based on patterns
  const generateSuggestions = (analysis: any): AISuggestion[] => {
    const suggestions: AISuggestion[] = [];

    // Energy optimization suggestions
    if (analysis.energyDistribution.high < 2) {
      suggestions.push({
        id: 'energy-high',
        type: 'tip',
        title: 'Schedule High-Energy Tasks',
        description: 'You have few high-energy blocks. Consider scheduling important work during your peak hours.',
        priority: 'medium',
        action: () => {
          // Auto-schedule high-energy blocks
          const importantBlocks = blocks.filter(block => 
            block.priority === 'high' || block.priority === 'urgent'
          );
          importantBlocks.forEach(block => {
            if (!block.scheduledFor) {
              const newDate = new Date(selectedDate);
              newDate.setHours(10, 0, 0, 0); // 10 AM
              addBlock({ ...block, scheduledFor: newDate, energyLevel: 'high' });
            }
          });
        }
      });
    }

    // Break optimization
    if (analysis.totalDuration > 300 && analysis.energyDistribution.low === 0) {
      suggestions.push({
        id: 'break-needed',
        type: 'warning',
        title: 'Add Break Blocks',
        description: `You have ${Math.round(analysis.totalDuration / 60)} hours scheduled without breaks. Consider adding 15-minute breaks every 2 hours.`,
        priority: 'high',
        action: () => {
          // Add break blocks
          for (let i = 2; i < analysis.totalDuration / 60; i += 2) {
            const breakTime = new Date(selectedDate);
            breakTime.setHours(9 + i, 0, 0, 0);
            
            addBlock({
              id: Date.now().toString() + i,
              title: 'Break',
              description: 'Take a well-deserved break',
              duration: 15,
              category: 'break',
              priority: 'medium',
              energyLevel: 'low',
              completed: false,
              createdAt: new Date(),
              scheduledFor: breakTime,
              tags: ['ai-suggested', 'break'],
            });
          }
        }
      });
    }

    // Category balance suggestions
    const workBlocks = analysis.categoryDistribution.work || 0;
    const personalBlocks = analysis.categoryDistribution.personal || 0;
    
    if (workBlocks > personalBlocks * 3) {
      suggestions.push({
        id: 'work-life-balance',
        type: 'tip',
        title: 'Work-Life Balance',
        description: 'Your schedule is heavily work-focused. Consider adding personal or wellness blocks.',
        priority: 'medium',
        action: () => {
          // Suggest personal blocks
          const personalSuggestions = [
            { title: 'Exercise', category: 'exercise' as BlockCategory, duration: 30 },
            { title: 'Meditation', category: 'personal' as BlockCategory, duration: 15 },
            { title: 'Hobby Time', category: 'creative' as BlockCategory, duration: 45 },
          ];
          
          personalSuggestions.forEach((suggestion, index) => {
            const blockTime = new Date(selectedDate);
            blockTime.setHours(18 + index, 0, 0, 0);
            
            addBlock({
              id: Date.now().toString() + 'personal' + index,
              title: suggestion.title,
              description: 'AI-suggested personal time',
              duration: suggestion.duration,
              category: suggestion.category,
              priority: 'medium',
              energyLevel: 'medium',
              completed: false,
              createdAt: new Date(),
              scheduledFor: blockTime,
              tags: ['ai-suggested', 'personal'],
            });
          });
        }
      });
    }

    // Time distribution optimization
    if (analysis.timeDistribution.morning < 60) {
      suggestions.push({
        id: 'morning-productivity',
        type: 'tip',
        title: 'Morning Productivity',
        description: 'You have little scheduled for the morning. Morning hours are often the most productive.',
        priority: 'low',
      });
    }

    return suggestions;
  };

  // Auto-generate optimal day schedule
  const generateOptimalDay = async () => {
    setIsGenerating(true);
    
    // Simulate AI processing
    await new Promise(resolve => setTimeout(resolve, 2000));
    
    const analysis = analyzeDay();
    const newSuggestions = generateSuggestions(analysis);
    setSuggestions(newSuggestions);
    
    setIsGenerating(false);
  };

  // Smart block scheduling based on energy patterns
  const scheduleSmartBlocks = (blocksToSchedule: TimeBlock[]) => {
    const workStart = parseInt(userPreferences.workStart.split(':')[0]);
    const workEnd = parseInt(userPreferences.workEnd.split(':')[0]);
    
    // Sort blocks by priority and energy requirements
    const sortedBlocks = blocksToSchedule.sort((a, b) => {
      const priorityOrder = { urgent: 4, high: 3, medium: 2, low: 1 };
      const energyOrder = { high: 3, medium: 2, low: 1 };
      
      if (priorityOrder[a.priority] !== priorityOrder[b.priority]) {
        return priorityOrder[b.priority] - priorityOrder[a.priority];
      }
      return energyOrder[b.energyLevel] - energyOrder[a.energyLevel];
    });

    // Schedule blocks based on energy patterns
    let currentTime = new Date(selectedDate);
    currentTime.setHours(workStart, 0, 0, 0);

    sortedBlocks.forEach((block, index) => {
      // High energy blocks in morning (9-11 AM)
      if (block.energyLevel === 'high' && block.category === 'work') {
        currentTime.setHours(9 + (index % 2), 0, 0, 0);
      }
      // Medium energy blocks in afternoon (1-3 PM)
      else if (block.energyLevel === 'medium') {
        currentTime.setHours(13 + (index % 2), 0, 0, 0);
      }
      // Low energy blocks in evening (4-6 PM)
      else if (block.energyLevel === 'low') {
        currentTime.setHours(16 + (index % 2), 0, 0, 0);
      }

      addBlock({
        ...block,
        scheduledFor: new Date(currentTime),
        tags: [...(block.tags || []), 'ai-scheduled'],
      });

      // Move to next available slot
      currentTime.setMinutes(currentTime.getMinutes() + block.duration + userPreferences.breakDuration);
    });
  };

  const getSuggestionIcon = (type: string) => {
    switch (type) {
      case 'schedule': return Calendar;
      case 'optimize': return TrendingUp;
      case 'warning': return Target;
      case 'tip': return Lightbulb;
      default: return Brain;
    }
  };

  const getSuggestionColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'text-red-600 bg-red-100';
      case 'medium': return 'text-yellow-600 bg-yellow-100';
      case 'low': return 'text-blue-600 bg-blue-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Brain className="w-6 h-6 text-purple-500" />
        <h3 className="text-lg font-semibold text-gray-900">AI Auto-Scheduler 2.0</h3>
        <div className="flex items-center space-x-1 px-2 py-1 bg-purple-100 text-purple-700 rounded-full text-xs font-medium">
          <Zap className="w-3 h-3" />
          <span>Pro</span>
        </div>
      </div>

      {/* AI Analysis */}
      <div className="mb-6">
        <button
          onClick={generateOptimalDay}
          disabled={isGenerating}
          className="w-full flex items-center justify-center space-x-2 px-6 py-4 bg-gradient-to-r from-purple-500 to-pink-500 text-white rounded-lg hover:from-purple-600 hover:to-pink-600 disabled:opacity-50 disabled:cursor-not-allowed transition-all"
        >
          {isGenerating ? (
            <>
              <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
              <span>AI is analyzing your patterns...</span>
            </>
          ) : (
            <>
              <Brain className="w-5 h-5" />
              <span>Generate Optimal Day Schedule</span>
            </>
          )}
        </button>
      </div>

      {/* AI Suggestions */}
      {suggestions.length > 0 && (
        <div className="space-y-4">
          <h4 className="font-medium text-gray-900">AI Suggestions</h4>
          {suggestions.map((suggestion) => {
            const Icon = getSuggestionIcon(suggestion.type);
            return (
              <div
                key={suggestion.id}
                className="p-4 border border-gray-200 rounded-lg hover:border-purple-300 transition-colors"
              >
                <div className="flex items-start space-x-3">
                  <div className={`p-2 rounded-lg ${getSuggestionColor(suggestion.priority)}`}>
                    <Icon className="w-5 h-5" />
                  </div>
                  
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <h5 className="font-medium text-gray-900">{suggestion.title}</h5>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getSuggestionColor(suggestion.priority)}`}>
                        {suggestion.priority}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mb-3">{suggestion.description}</p>
                    
                    {suggestion.action && (
                      <button
                        onClick={suggestion.action}
                        className="px-4 py-2 bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors text-sm"
                      >
                        Apply Suggestion
                      </button>
                    )}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}

      {/* Smart Scheduling Tools */}
      <div className="mt-6 p-4 bg-gray-50 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-3">Smart Scheduling Tools</h4>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <button
            onClick={() => {
              const unscheduledBlocks = blocks.filter(block => !block.scheduledFor);
              scheduleSmartBlocks(unscheduledBlocks);
            }}
            className="flex items-center space-x-2 px-4 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
          >
            <Clock className="w-5 h-5" />
            <span>Auto-Schedule Unscheduled Blocks</span>
          </button>
          
          <button
            onClick={() => {
              // Optimize existing schedule
              const todayBlocks = blocks.filter(block => 
                block.scheduledFor && 
                new Date(block.scheduledFor).toDateString() === selectedDate.toDateString()
              );
              scheduleSmartBlocks(todayBlocks);
            }}
            className="flex items-center space-x-2 px-4 py-3 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors"
          >
            <TrendingUp className="w-5 h-5" />
            <span>Optimize Today's Schedule</span>
          </button>
        </div>
      </div>

      {/* AI Insights */}
      <div className="mt-6 p-4 bg-gradient-to-r from-blue-50 to-purple-50 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-2">AI Insights</h4>
        <div className="text-sm text-gray-700 space-y-1">
          <p>• AI learns from your completion patterns and energy levels</p>
          <p>• Suggests optimal scheduling based on your productivity peaks</p>
          <p>• Automatically balances work, breaks, and personal time</p>
          <p>• Adapts to your preferences and working style</p>
        </div>
      </div>
    </div>
  );
};

export default AIAutoScheduler;
