import React, { useState, useEffect } from 'react';
import { Play, Pause, Square, Volume2, VolumeX, Settings, X, RotateCcw } from 'lucide-react';
import { useStore } from '../store/useStore';
import { format, differenceInMinutes } from 'date-fns';

const FocusMode: React.FC = () => {
  const { 
    activeFocusSession, 
    endFocusSession, 
    blocks, 
    setFocusMode,
    completeBlock 
  } = useStore();
  
  const [timeLeft, setTimeLeft] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [isMuted, setIsMuted] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  // Detect mobile device
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  const currentBlock = activeFocusSession 
    ? blocks.find(block => block.id === activeFocusSession.blockId)
    : null;

  useEffect(() => {
    if (!currentBlock) return;

    setTimeLeft(currentBlock.duration);
  }, [currentBlock]);

  useEffect(() => {
    let interval: NodeJS.Timeout;

    if (isRunning && timeLeft > 0) {
      interval = setInterval(() => {
        setTimeLeft(prev => {
          if (prev <= 1) {
            setIsRunning(false);
            handleComplete();
            return 0;
          }
          return prev - 1;
        });
      }, 1000);
    }

    return () => clearInterval(interval);
  }, [isRunning, timeLeft]);

  const handleStart = () => {
    setIsRunning(true);
  };

  const handlePause = () => {
    setIsRunning(false);
  };

  const handleStop = () => {
    setIsRunning(false);
    setTimeLeft(currentBlock?.duration || 0);
  };

  const handleComplete = () => {
    if (currentBlock) {
      completeBlock(currentBlock.id);
    }
    endFocusSession();
    setFocusMode(false);
  };

  const handleExit = () => {
    setIsRunning(false);
    endFocusSession();
    setFocusMode(false);
  };

  const formatTime = (minutes: number) => {
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    return hours > 0 ? `${hours}:${mins.toString().padStart(2, '0')}` : `${mins}:00`;
  };

  const progress = currentBlock ? ((currentBlock.duration - timeLeft) / currentBlock.duration) * 100 : 0;

  if (!currentBlock || !activeFocusSession) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-900 to-blue-900 flex items-center justify-center">
        <div className="text-center text-white">
          <h2 className="text-2xl font-bold mb-4">No Active Focus Session</h2>
          <button
            onClick={handleExit}
            className="px-6 py-3 bg-white text-slate-900 rounded-lg hover:bg-gray-100 transition-colors"
          >
            Return to Dashboard
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 to-blue-900 text-white">
      {/* Header */}
      <div className="flex items-center justify-between p-6">
        <div className="flex items-center space-x-4">
          <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
            <Play className="w-7 h-7" />
          </div>
          <div>
            <h1 className="text-2xl font-bold">Focus Mode</h1>
            <p className="text-white/70">Stay focused, stay productive</p>
          </div>
        </div>

        <div className="flex items-center space-x-4">
          <button
            onClick={() => setIsMuted(!isMuted)}
            className="p-2 hover:bg-white/20 rounded-lg transition-colors"
          >
            {isMuted ? <VolumeX className="w-6 h-6" /> : <Volume2 className="w-6 h-6" />}
          </button>
          
          <button
            onClick={() => setShowSettings(!showSettings)}
            className="p-2 hover:bg-white/20 rounded-lg transition-colors"
          >
            <Settings className="w-6 h-6" />
          </button>
          
          <button
            onClick={handleExit}
            className="p-2 hover:bg-white/20 rounded-lg transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>
      </div>

      {/* Main Focus Area */}
      <div className="flex flex-col items-center justify-center min-h-[calc(100vh-200px)] px-6">
        {/* Block Info */}
        <div className="text-center mb-12">
          <h2 className="text-4xl font-bold mb-4">{currentBlock.title}</h2>
          {currentBlock.description && (
            <p className="text-xl text-white/70 mb-2">{currentBlock.description}</p>
          )}
          <div className="flex items-center justify-center space-x-4 text-white/60">
            <span>Category: {currentBlock.category}</span>
            <span>•</span>
            <span>Priority: {currentBlock.priority}</span>
            <span>•</span>
            <span>Energy: {currentBlock.energyLevel}</span>
          </div>
        </div>

        {/* Timer Circle */}
        <div className="relative mb-8 md:mb-12">
          <div className={`${isMobile ? 'w-48 h-48' : 'w-64 h-64'} relative`}>
            {/* Background Circle */}
            <svg className="w-full h-full transform -rotate-90" viewBox="0 0 100 100">
              <circle
                cx="50"
                cy="50"
                r="45"
                fill="none"
                stroke="rgba(255,255,255,0.1)"
                strokeWidth="8"
              />
              {/* Progress Circle */}
              <circle
                cx="50"
                cy="50"
                r="45"
                fill="none"
                stroke="url(#gradient)"
                strokeWidth="8"
                strokeLinecap="round"
                strokeDasharray={`${2 * Math.PI * 45}`}
                strokeDashoffset={`${2 * Math.PI * 45 * (1 - progress / 100)}`}
                className="transition-all duration-1000 ease-out"
              />
              <defs>
                <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                  <stop offset="0%" stopColor="#3b82f6" />
                  <stop offset="100%" stopColor="#8b5cf6" />
                </linearGradient>
              </defs>
            </svg>
            
            {/* Timer Text */}
            <div className="absolute inset-0 flex flex-col items-center justify-center">
              <div className={`${isMobile ? 'text-4xl' : 'text-6xl'} font-bold mb-2`}>
                {formatTime(timeLeft)}
              </div>
              <div className="text-white/60 text-sm md:text-base">
                {isRunning ? 'Focusing...' : 'Paused'}
              </div>
            </div>
          </div>
        </div>

        {/* Controls */}
        <div className={`flex items-center ${isMobile ? 'space-x-4' : 'space-x-6'}`}>
          {!isRunning ? (
            <button
              onClick={handleStart}
              className={`flex items-center space-x-2 ${isMobile ? 'px-6 py-3 text-lg' : 'px-8 py-4 text-xl'} bg-green-500 hover:bg-green-600 rounded-full font-semibold transition-colors`}
            >
              <Play className="w-5 h-5 md:w-6 md:h-6" />
              <span>{isMobile ? 'Start' : 'Start Focus'}</span>
            </button>
          ) : (
            <button
              onClick={handlePause}
              className={`flex items-center space-x-2 ${isMobile ? 'px-6 py-3 text-lg' : 'px-8 py-4 text-xl'} bg-yellow-500 hover:bg-yellow-600 rounded-full font-semibold transition-colors`}
            >
              <Pause className="w-5 h-5 md:w-6 md:h-6" />
              <span>Pause</span>
            </button>
          )}
          
          <button
            onClick={handleStop}
            className={`flex items-center space-x-2 ${isMobile ? 'px-6 py-3 text-lg' : 'px-8 py-4 text-xl'} bg-red-500 hover:bg-red-600 rounded-full font-semibold transition-colors`}
          >
            <Square className="w-5 h-5 md:w-6 md:h-6" />
            <span>Stop</span>
          </button>

          {isMobile && (
            <button
              onClick={handleStop}
              className="flex items-center space-x-2 px-4 py-3 bg-gray-500 hover:bg-gray-600 rounded-full text-lg font-semibold transition-colors"
            >
              <RotateCcw className="w-5 h-5" />
            </button>
          )}
        </div>

        {/* Complete Button */}
        {timeLeft === 0 && (
          <button
            onClick={handleComplete}
            className="mt-8 px-12 py-4 bg-gradient-to-r from-green-500 to-green-600 hover:from-green-600 hover:to-green-700 rounded-full text-2xl font-bold transition-all transform hover:scale-105"
          >
            Complete Block! 🎉
          </button>
        )}
      </div>

      {/* Settings Panel */}
      {showSettings && (
        <div className="absolute top-20 right-6 bg-white/10 backdrop-blur-lg rounded-xl p-6 w-80">
          <h3 className="text-xl font-bold mb-4">Focus Settings</h3>
          
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium mb-2">Ambient Sounds</label>
              <select className="w-full px-3 py-2 bg-white/20 rounded-lg text-white">
                <option value="none">None</option>
                <option value="rain">Rain</option>
                <option value="forest">Forest</option>
                <option value="ocean">Ocean</option>
                <option value="coffee">Coffee Shop</option>
              </select>
            </div>
            
            <div>
              <label className="block text-sm font-medium mb-2">Notifications</label>
              <div className="space-y-2">
                <label className="flex items-center space-x-2">
                  <input type="checkbox" className="rounded" defaultChecked />
                  <span>Block start/end</span>
                </label>
                <label className="flex items-center space-x-2">
                  <input type="checkbox" className="rounded" />
                  <span>Break reminders</span>
                </label>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FocusMode;
