import React from 'react';
import { Calendar, Focus, Settings, Trophy, Menu } from 'lucide-react';
import { useStore } from '../store/useStore';
import { format } from 'date-fns';

interface HeaderProps {
  isMobile?: boolean;
  onMenuToggle?: () => void;
}

const Header: React.FC<HeaderProps> = ({ isMobile = false, onMenuToggle }) => {
  const { user, selectedDate, setSelectedDate, getStreak, getLevel } = useStore();
  const streak = getStreak();
  const level = getLevel();

  return (
    <header className="bg-white rounded-xl shadow-lg p-4 md:p-6 mb-6">
      <div className="flex items-center justify-between">
        {/* Logo and Title */}
        <div className="flex items-center space-x-3 md:space-x-4">
          <div className="w-10 h-10 md:w-12 md:h-12 bg-gradient-to-br from-primary-500 to-primary-600 rounded-xl flex items-center justify-center">
            <Focus className="w-6 h-6 md:w-7 md:h-7 text-white" />
          </div>
          <div>
            <h1 className="text-2xl md:text-3xl font-bold text-gray-900">FocusBlocks</h1>
            <p className="text-sm md:text-base text-gray-600 hidden sm:block">Build your day, block by block</p>
          </div>
        </div>

        {/* Mobile Menu Button */}
        {isMobile && onMenuToggle && (
          <button 
            onClick={onMenuToggle}
            className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <Menu className="w-6 h-6" />
          </button>
        )}

        {/* Desktop Stats */}
        <div className="hidden md:flex items-center space-x-6">
          {/* Level & Streak */}
          <div className="flex items-center space-x-4">
            <div className="text-center">
              <div className="flex items-center space-x-1">
                <Trophy className="w-5 h-5 text-yellow-500" />
                <span className="text-sm font-medium text-gray-600">Level</span>
              </div>
              <div className="text-2xl font-bold text-primary-600">{level}</div>
            </div>
            
            <div className="text-center">
              <div className="flex items-center space-x-1">
                <div className="w-5 h-5 bg-orange-500 rounded-full"></div>
                <span className="text-sm font-medium text-gray-600">Streak</span>
              </div>
              <div className="text-2xl font-bold text-orange-500">{streak}</div>
            </div>
          </div>

          {/* Date Selector */}
          <div className="flex items-center space-x-2">
            <Calendar className="w-5 h-5 text-gray-500" />
            <input
              type="date"
              value={format(selectedDate, 'yyyy-MM-dd')}
              onChange={(e) => setSelectedDate(new Date(e.target.value))}
              className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
            />
          </div>

          {/* Settings */}
          <button className="p-2 text-gray-500 hover:text-gray-700 hover:bg-gray-100 rounded-lg transition-colors">
            <Settings className="w-6 h-6" />
          </button>
        </div>
      </div>

      {/* Mobile Stats */}
      <div className="md:hidden mt-4 flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <div className="text-center">
            <div className="flex items-center space-x-1">
              <Trophy className="w-4 h-4 text-yellow-500" />
              <span className="text-xs font-medium text-gray-600">Level</span>
            </div>
            <div className="text-lg font-bold text-primary-600">{level}</div>
          </div>
          
          <div className="text-center">
            <div className="flex items-center space-x-1">
              <div className="w-4 h-4 bg-orange-500 rounded-full"></div>
              <span className="text-xs font-medium text-gray-600">Streak</span>
            </div>
            <div className="text-lg font-bold text-orange-500">{streak}</div>
          </div>
        </div>

        <div className="flex items-center space-x-2">
          <Calendar className="w-4 h-4 text-gray-500" />
          <input
            type="date"
            value={format(selectedDate, 'yyyy-MM-dd')}
            onChange={(e) => setSelectedDate(new Date(e.target.value))}
            className="px-2 py-1 text-sm border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
          />
        </div>
      </div>
    </header>
  );
};

export default Header;