import React, { useState } from 'react';
import { Crown, Check, X, Star, Zap, Users, Calendar, Brain, Trophy, Shield, Cloud, Smartphone } from 'lucide-react';

interface Plan {
  id: string;
  name: string;
  price: number;
  period: 'monthly' | 'yearly';
  description: string;
  features: string[];
  popular?: boolean;
  current?: boolean;
}

interface ProFeature {
  id: string;
  name: string;
  description: string;
  icon: React.ComponentType<any>;
  category: 'ai' | 'sync' | 'social' | 'analytics' | 'customization';
  locked: boolean;
}

const FreemiumModel: React.FC = () => {
  const [activeTab, setActiveTab] = useState<'plans' | 'features' | 'billing'>('plans');
  const [selectedPlan, setSelectedPlan] = useState<string>('free');
  const [isYearly, setIsYearly] = useState(false);

  const plans: Plan[] = [
    {
      id: 'free',
      name: 'Free',
      price: 0,
      period: 'monthly',
      description: 'Perfect for getting started',
      features: [
        'Up to 5 blocks per day',
        'Basic focus timer',
        '1 theme',
        'Basic analytics',
        'Mobile app access',
        'Offline support'
      ],
      current: true
    },
    {
      id: 'pro',
      name: 'Pro',
      price: isYearly ? 8 : 12,
      period: isYearly ? 'yearly' : 'monthly',
      description: 'For serious productivity enthusiasts',
      features: [
        'Unlimited blocks',
        'AI Auto-Scheduler 2.0',
        'Calendar sync (Google, Outlook, Apple)',
        'Productivity app sync (Notion, Todoist, Trello)',
        'Advanced analytics & insights',
        'All themes & customizations',
        'Cloud sync across devices',
        'Priority support',
        'Export data',
        'Advanced gamification'
      ],
      popular: true
    },
    {
      id: 'team',
      name: 'Team',
      price: isYearly ? 15 : 20,
      period: isYearly ? 'yearly' : 'monthly',
      description: 'For teams and organizations',
      features: [
        'Everything in Pro',
        'Shared team workspaces',
        'Team analytics & reporting',
        'Admin dashboard',
        'User management',
        'Custom branding',
        'API access',
        'SSO integration',
        'Dedicated support',
        'Custom integrations'
      ]
    }
  ];

  const proFeatures: ProFeature[] = [
    {
      id: 'ai-scheduler',
      name: 'AI Auto-Scheduler 2.0',
      description: 'Smart day building with pattern learning and optimization',
      icon: Brain,
      category: 'ai',
      locked: true
    },
    {
      id: 'calendar-sync',
      name: 'Calendar Sync',
      description: 'Connect Google, Outlook, and Apple Calendar',
      icon: Calendar,
      category: 'sync',
      locked: true
    },
    {
      id: 'productivity-sync',
      name: 'Productivity App Sync',
      description: 'Import from Notion, Todoist, Trello, and more',
      icon: Zap,
      category: 'sync',
      locked: true
    },
    {
      id: 'social-features',
      name: 'Social Features',
      description: 'Live focus rooms, shared towers, accountability partners',
      icon: Users,
      category: 'social',
      locked: true
    },
    {
      id: 'advanced-analytics',
      name: 'Advanced Analytics',
      description: 'Detailed insights, trends, and productivity reports',
      icon: Star,
      category: 'analytics',
      locked: true
    },
    {
      id: 'cloud-sync',
      name: 'Cloud Sync',
      description: 'Seamless sync across all your devices',
      icon: Cloud,
      category: 'sync',
      locked: true
    },
    {
      id: 'mobile-widgets',
      name: 'Mobile Widgets',
      description: 'Home screen widgets for quick access',
      icon: Smartphone,
      category: 'customization',
      locked: true
    },
    {
      id: 'advanced-gamification',
      name: 'Advanced Gamification',
      description: 'Achievements, rewards, leaderboards, and challenges',
      icon: Trophy,
      category: 'customization',
      locked: true
    }
  ];

  const getFeatureIcon = (category: string) => {
    switch (category) {
      case 'ai': return '🤖';
      case 'sync': return '🔄';
      case 'social': return '👥';
      case 'analytics': return '📊';
      case 'customization': return '🎨';
      default: return '⭐';
    }
  };

  const getFeatureColor = (category: string) => {
    switch (category) {
      case 'ai': return 'text-purple-600 bg-purple-100';
      case 'sync': return 'text-blue-600 bg-blue-100';
      case 'social': return 'text-green-600 bg-green-100';
      case 'analytics': return 'text-orange-600 bg-orange-100';
      case 'customization': return 'text-pink-600 bg-pink-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const handleUpgrade = (planId: string) => {
    if (planId === 'free') return;
    
    // In real app, this would redirect to payment
    console.log(`Upgrading to ${planId} plan`);
    alert(`Redirecting to payment for ${planId} plan...`);
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Crown className="w-6 h-6 text-yellow-500" />
        <h3 className="text-lg font-semibold text-gray-900">FocusBlocks Pro</h3>
        <div className="px-2 py-1 bg-yellow-100 text-yellow-700 rounded-full text-xs font-medium">
          Upgrade Available
        </div>
      </div>

      {/* Tab Navigation */}
      <div className="flex space-x-1 mb-6 bg-gray-100 rounded-lg p-1">
        {[
          { id: 'plans', label: 'Plans', icon: Crown },
          { id: 'features', label: 'Features', icon: Star },
          { id: 'billing', label: 'Billing', icon: Shield },
        ].map((tab) => {
          const Icon = tab.icon;
          return (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`flex-1 flex items-center justify-center space-x-2 px-3 py-2 rounded-md transition-colors ${
                activeTab === tab.id
                  ? 'bg-white text-blue-600 shadow-sm'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              <Icon className="w-4 h-4" />
              <span className="text-sm font-medium">{tab.label}</span>
            </button>
          );
        })}
      </div>

      {/* Tab Content */}
      <div className="space-y-6">
        {activeTab === 'plans' && (
          <div className="space-y-6">
            {/* Billing Toggle */}
            <div className="flex items-center justify-center space-x-4">
              <span className={`text-sm font-medium ${!isYearly ? 'text-gray-900' : 'text-gray-500'}`}>
                Monthly
              </span>
              <button
                onClick={() => setIsYearly(!isYearly)}
                className={`relative inline-flex h-6 w-11 items-center rounded-full transition-colors ${
                  isYearly ? 'bg-blue-600' : 'bg-gray-300'
                }`}
              >
                <span
                  className={`inline-block h-4 w-4 transform rounded-full bg-white transition-transform ${
                    isYearly ? 'translate-x-6' : 'translate-x-1'
                  }`}
                />
              </button>
              <span className={`text-sm font-medium ${isYearly ? 'text-gray-900' : 'text-gray-500'}`}>
                Yearly
              </span>
              {isYearly && (
                <span className="px-2 py-1 bg-green-100 text-green-700 rounded-full text-xs font-medium">
                  Save 33%
                </span>
              )}
            </div>

            {/* Plans Grid */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {plans.map((plan) => (
                <div
                  key={plan.id}
                  className={`relative p-6 rounded-xl border-2 ${
                    plan.popular
                      ? 'border-blue-200 bg-blue-50'
                      : plan.current
                      ? 'border-green-200 bg-green-50'
                      : 'border-gray-200 bg-gray-50'
                  }`}
                >
                  {plan.popular && (
                    <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                      <div className="px-3 py-1 bg-blue-500 text-white rounded-full text-xs font-medium">
                        Most Popular
                      </div>
                    </div>
                  )}
                  
                  {plan.current && (
                    <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                      <div className="px-3 py-1 bg-green-500 text-white rounded-full text-xs font-medium">
                        Current Plan
                      </div>
                    </div>
                  )}

                  <div className="text-center mb-4">
                    <h3 className="text-xl font-bold text-gray-900">{plan.name}</h3>
                    <p className="text-sm text-gray-600">{plan.description}</p>
                  </div>

                  <div className="text-center mb-6">
                    <div className="flex items-baseline justify-center">
                      <span className="text-4xl font-bold text-gray-900">${plan.price}</span>
                      <span className="text-gray-600 ml-1">/{plan.period === 'yearly' ? 'year' : 'month'}</span>
                    </div>
                    {isYearly && plan.id !== 'free' && (
                      <div className="text-sm text-green-600 font-medium">
                        Save ${(12 - plan.price) * 12} per year
                      </div>
                    )}
                  </div>

                  <ul className="space-y-3 mb-6">
                    {plan.features.map((feature, index) => (
                      <li key={index} className="flex items-center space-x-3">
                        <Check className="w-5 h-5 text-green-500 flex-shrink-0" />
                        <span className="text-sm text-gray-700">{feature}</span>
                      </li>
                    ))}
                  </ul>

                  <button
                    onClick={() => handleUpgrade(plan.id)}
                    disabled={plan.current}
                    className={`w-full py-3 px-4 rounded-lg font-medium transition-colors ${
                      plan.current
                        ? 'bg-gray-300 text-gray-500 cursor-not-allowed'
                        : plan.popular
                        ? 'bg-blue-500 text-white hover:bg-blue-600'
                        : 'bg-gray-500 text-white hover:bg-gray-600'
                    }`}
                  >
                    {plan.current ? 'Current Plan' : `Upgrade to ${plan.name}`}
                  </button>
                </div>
              ))}
            </div>
          </div>
        )}

        {activeTab === 'features' && (
          <div className="space-y-6">
            <div>
              <h4 className="font-medium text-gray-900 mb-4">Pro Features</h4>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {proFeatures.map((feature) => {
                  const Icon = feature.icon;
                  return (
                    <div
                      key={feature.id}
                      className={`p-4 rounded-lg border-2 ${
                        feature.locked
                          ? 'border-gray-200 bg-gray-50'
                          : 'border-green-200 bg-green-50'
                      }`}
                    >
                      <div className="flex items-start space-x-3">
                        <div className={`p-2 rounded-lg ${getFeatureColor(feature.category)}`}>
                          <Icon className="w-5 h-5" />
                        </div>
                        <div className="flex-1">
                          <div className="flex items-center space-x-2 mb-1">
                            <h5 className="font-medium text-gray-900">{feature.name}</h5>
                            <span className={`px-2 py-1 rounded-full text-xs font-medium ${getFeatureColor(feature.category)}`}>
                              {getFeatureIcon(feature.category)}
                            </span>
                            {feature.locked && (
                              <div className="w-4 h-4 text-gray-400">
                                <X className="w-4 h-4" />
                              </div>
                            )}
                          </div>
                          <p className="text-sm text-gray-600 mb-3">{feature.description}</p>
                          {feature.locked && (
                            <button className="px-3 py-1 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm">
                              Upgrade to Unlock
                            </button>
                          )}
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
        )}

        {activeTab === 'billing' && (
          <div className="space-y-6">
            <div>
              <h4 className="font-medium text-gray-900 mb-4">Billing Information</h4>
              <div className="p-4 bg-gray-50 rounded-lg">
                <div className="text-center">
                  <div className="text-2xl font-bold text-gray-900 mb-2">Free Plan</div>
                  <p className="text-gray-600 mb-4">You're currently on the free plan</p>
                  <button className="px-6 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors">
                    Upgrade to Pro
                  </button>
                </div>
              </div>
            </div>

            <div>
              <h4 className="font-medium text-gray-900 mb-4">Payment Methods</h4>
              <div className="p-4 bg-gray-50 rounded-lg">
                <p className="text-gray-600 text-center">No payment method on file</p>
              </div>
            </div>

            <div>
              <h4 className="font-medium text-gray-900 mb-4">Billing History</h4>
              <div className="p-4 bg-gray-50 rounded-lg">
                <p className="text-gray-600 text-center">No billing history available</p>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Upgrade CTA */}
      <div className="mt-8 p-6 bg-gradient-to-r from-blue-50 to-purple-50 rounded-lg border border-blue-200">
        <div className="text-center">
          <h4 className="text-lg font-semibold text-gray-900 mb-2">
            Ready to unlock your full potential?
          </h4>
          <p className="text-gray-600 mb-4">
            Join thousands of users who have transformed their productivity with FocusBlocks Pro
          </p>
          <div className="flex justify-center space-x-4">
            <button className="px-6 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors">
              Start Free Trial
            </button>
            <button className="px-6 py-3 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors">
              View All Features
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default FreemiumModel;
