import React, { useState, useEffect } from 'react';
import { ExternalLink, RefreshCw, CheckCircle, AlertCircle, Settings, Zap, Database, List, Trello } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock, BlockCategory, Priority, EnergyLevel } from '../types';

interface AppConnection {
  id: string;
  name: string;
  icon: string;
  connected: boolean;
  lastSync: Date | null;
  syncStatus: 'idle' | 'syncing' | 'success' | 'error';
  itemsCount: number;
}

interface SyncItem {
  id: string;
  title: string;
  description?: string;
  source: 'notion' | 'todoist' | 'trello';
  priority: Priority;
  dueDate?: Date;
  tags: string[];
  status: 'pending' | 'in_progress' | 'completed';
  url?: string;
  icon: string;
}

const ProductivityAppSync: React.FC = () => {
  const { addBlock } = useStore();
  const [connections, setConnections] = useState<AppConnection[]>([
    {
      id: 'notion',
      name: 'Notion',
      icon: '📝',
      connected: false,
      lastSync: null,
      syncStatus: 'idle',
      itemsCount: 0
    },
    {
      id: 'todoist',
      name: 'Todoist',
      icon: '✅',
      connected: false,
      lastSync: null,
      syncStatus: 'idle',
      itemsCount: 0
    },
    {
      id: 'trello',
      name: 'Trello',
      icon: '📋',
      connected: false,
      lastSync: null,
      syncStatus: 'idle',
      itemsCount: 0
    }
  ]);

  const [syncItems, setSyncItems] = useState<SyncItem[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [selectedItems, setSelectedItems] = useState<string[]>([]);

  // Mock sync items data
  const mockSyncItems: SyncItem[] = [
    {
      id: 'notion-1',
      title: 'Complete project proposal',
      description: 'Write and submit the Q1 project proposal by Friday',
      source: 'notion',
      priority: 'high',
      dueDate: new Date(Date.now() + 3 * 24 * 60 * 60 * 1000),
      tags: ['work', 'urgent'],
      status: 'pending',
      url: 'https://notion.so/project-proposal',
      icon: '📝'
    },
    {
      id: 'todoist-1',
      title: 'Grocery shopping',
      description: 'Buy ingredients for weekend meal prep',
      source: 'todoist',
      priority: 'medium',
      dueDate: new Date(Date.now() + 1 * 24 * 60 * 60 * 1000),
      tags: ['personal', 'shopping'],
      status: 'pending',
      icon: '🛒'
    },
    {
      id: 'trello-1',
      title: 'Design review meeting',
      description: 'Review new UI designs with the team',
      source: 'trello',
      priority: 'high',
      dueDate: new Date(Date.now() + 2 * 24 * 60 * 60 * 1000),
      tags: ['work', 'meeting'],
      status: 'in_progress',
      url: 'https://trello.com/c/design-review',
      icon: '🎨'
    },
    {
      id: 'notion-2',
      title: 'Read industry report',
      description: 'Study the latest market trends and insights',
      source: 'notion',
      priority: 'low',
      tags: ['learning', 'research'],
      status: 'pending',
      icon: '📊'
    },
    {
      id: 'todoist-2',
      title: 'Call dentist',
      description: 'Schedule annual checkup',
      source: 'todoist',
      priority: 'medium',
      tags: ['personal', 'health'],
      status: 'pending',
      icon: '🦷'
    }
  ];

  const connectApp = async (appId: string) => {
    setIsLoading(true);
    
    try {
      // Simulate API connection
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setConnections(connections.map(conn => 
        conn.id === appId 
          ? { 
              ...conn, 
              connected: true, 
              lastSync: new Date(),
              syncStatus: 'success',
              itemsCount: mockSyncItems.filter(item => item.source === appId).length
            }
          : conn
      ));
      
      // Load sync items for this app
      const appItems = mockSyncItems.filter(item => item.source === appId);
      setSyncItems(prev => [...prev, ...appItems]);
      
    } catch (error) {
      console.error('Connection failed:', error);
      setConnections(connections.map(conn => 
        conn.id === appId 
          ? { ...conn, syncStatus: 'error' }
          : conn
      ));
    } finally {
      setIsLoading(false);
    }
  };

  const disconnectApp = (appId: string) => {
    setConnections(connections.map(conn => 
      conn.id === appId 
        ? { 
            ...conn, 
            connected: false, 
            lastSync: null,
            syncStatus: 'idle',
            itemsCount: 0
          }
        : conn
    ));
    
    // Remove items from this app
    setSyncItems(prev => prev.filter(item => item.source !== appId));
  };

  const syncApp = async (appId: string) => {
    const connection = connections.find(conn => conn.id === appId);
    if (!connection?.connected) return;

    setConnections(connections.map(conn => 
      conn.id === appId 
        ? { ...conn, syncStatus: 'syncing' }
        : conn
    ));

    try {
      // Simulate sync process
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      setConnections(connections.map(conn => 
        conn.id === appId 
          ? { 
              ...conn, 
              lastSync: new Date(),
              syncStatus: 'success'
            }
          : conn
      ));
      
    } catch (error) {
      console.error('Sync failed:', error);
      setConnections(connections.map(conn => 
        conn.id === appId 
          ? { ...conn, syncStatus: 'error' }
          : conn
      ));
    }
  };

  const convertToBlock = (item: SyncItem): TimeBlock => {
    // Determine category based on source and tags
    let category: BlockCategory = 'work';
    if (item.tags.includes('personal') || item.tags.includes('health')) {
      category = 'personal';
    } else if (item.tags.includes('learning') || item.tags.includes('study')) {
      category = 'study';
    } else if (item.tags.includes('meeting')) {
      category = 'meeting';
    } else if (item.tags.includes('shopping')) {
      category = 'personal';
    }

    // Determine energy level based on priority and time
    let energyLevel: EnergyLevel = 'medium';
    if (item.priority === 'high' || item.priority === 'urgent') {
      energyLevel = 'high';
    } else if (item.priority === 'low') {
      energyLevel = 'low';
    }

    // Estimate duration based on title and description
    let duration = 30; // default
    if (item.title.toLowerCase().includes('meeting')) {
      duration = 60;
    } else if (item.title.toLowerCase().includes('read') || item.title.toLowerCase().includes('study')) {
      duration = 45;
    } else if (item.title.toLowerCase().includes('call')) {
      duration = 15;
    } else if (item.title.toLowerCase().includes('shopping')) {
      duration = 90;
    }

    return {
      id: `sync-${item.id}`,
      title: item.title,
      description: item.description || `Imported from ${item.source}`,
      duration,
      category,
      priority: item.priority,
      energyLevel,
      completed: item.status === 'completed',
      createdAt: new Date(),
      scheduledFor: item.dueDate || new Date(),
      tags: [...item.tags, `imported-${item.source}`],
    };
  };

  const importSelectedItems = () => {
    const selectedItemsData = syncItems.filter(item => selectedItems.includes(item.id));
    
    selectedItemsData.forEach(item => {
      const block = convertToBlock(item);
      addBlock(block);
    });

    // Remove imported items
    setSyncItems(prev => prev.filter(item => !selectedItems.includes(item.id)));
    setSelectedItems([]);
  };

  const getAppIcon = (appId: string) => {
    switch (appId) {
      case 'notion': return Database;
      case 'todoist': return List;
      case 'trello': return Trello;
      default: return ExternalLink;
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'syncing': return <RefreshCw className="w-4 h-4 animate-spin" />;
      case 'success': return <CheckCircle className="w-4 h-4 text-green-500" />;
      case 'error': return <AlertCircle className="w-4 h-4 text-red-500" />;
      default: return <ExternalLink className="w-4 h-4" />;
    }
  };

  const getPriorityColor = (priority: Priority) => {
    switch (priority) {
      case 'urgent': return 'text-red-600 bg-red-100';
      case 'high': return 'text-orange-600 bg-orange-100';
      case 'medium': return 'text-yellow-600 bg-yellow-100';
      case 'low': return 'text-blue-600 bg-blue-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Zap className="w-6 h-6 text-purple-500" />
        <h3 className="text-lg font-semibold text-gray-900">Productivity App Sync</h3>
      </div>

      {/* App Connections */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
        {connections.map((connection) => {
          const AppIcon = getAppIcon(connection.id);
          return (
            <div
              key={connection.id}
              className={`p-4 rounded-lg border-2 ${
                connection.connected 
                  ? 'border-green-200 bg-green-50' 
                  : 'border-gray-200 bg-gray-50'
              }`}
            >
              <div className="flex items-center space-x-3 mb-3">
                <div className="text-2xl">{connection.icon}</div>
                <div className="flex-1">
                  <h4 className="font-medium text-gray-900">{connection.name}</h4>
                  <div className="flex items-center space-x-2">
                    {getStatusIcon(connection.syncStatus)}
                    <span className="text-sm text-gray-600">
                      {connection.connected ? 'Connected' : 'Not connected'}
                    </span>
                  </div>
                </div>
                <AppIcon className="w-5 h-5 text-gray-400" />
              </div>

              {connection.connected && (
                <div className="text-sm text-gray-600 mb-3">
                  <div>{connection.itemsCount} items available</div>
                  {connection.lastSync && (
                    <div>Last synced: {connection.lastSync.toLocaleTimeString()}</div>
                  )}
                </div>
              )}

              <div className="flex space-x-2">
                {connection.connected ? (
                  <>
                    <button
                      onClick={() => syncApp(connection.id)}
                      disabled={connection.syncStatus === 'syncing'}
                      className="flex-1 px-3 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                    >
                      {connection.syncStatus === 'syncing' ? 'Syncing...' : 'Sync'}
                    </button>
                    <button
                      onClick={() => disconnectApp(connection.id)}
                      className="px-3 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors text-sm"
                    >
                      Disconnect
                    </button>
                  </>
                ) : (
                  <button
                    onClick={() => connectApp(connection.id)}
                    disabled={isLoading}
                    className="w-full px-3 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                  >
                    {isLoading ? 'Connecting...' : 'Connect'}
                  </button>
                )}
              </div>
            </div>
          );
        })}
      </div>

      {/* Sync Items */}
      {syncItems.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h4 className="font-medium text-gray-900">Available Items</h4>
            <div className="flex space-x-2">
              <button
                onClick={() => setSelectedItems(syncItems.map(item => item.id))}
                className="px-3 py-1 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors text-sm"
              >
                Select All
              </button>
              <button
                onClick={() => setSelectedItems([])}
                className="px-3 py-1 bg-gray-500 text-white rounded-lg hover:bg-gray-600 transition-colors text-sm"
              >
                Clear
              </button>
              <button
                onClick={importSelectedItems}
                disabled={selectedItems.length === 0}
                className="px-3 py-1 bg-purple-500 text-white rounded-lg hover:bg-purple-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
              >
                Import Selected ({selectedItems.length})
              </button>
            </div>
          </div>

          <div className="space-y-2">
            {syncItems.map((item) => (
              <div
                key={item.id}
                className={`p-4 rounded-lg border-2 ${
                  selectedItems.includes(item.id)
                    ? 'border-purple-200 bg-purple-50'
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-start space-x-3">
                  <input
                    type="checkbox"
                    checked={selectedItems.includes(item.id)}
                    onChange={(e) => {
                      if (e.target.checked) {
                        setSelectedItems([...selectedItems, item.id]);
                      } else {
                        setSelectedItems(selectedItems.filter(id => id !== item.id));
                      }
                    }}
                    className="mt-1 rounded"
                  />
                  
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <span className="text-lg">{item.icon}</span>
                      <h5 className="font-medium text-gray-900">{item.title}</h5>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getPriorityColor(item.priority)}`}>
                        {item.priority}
                      </span>
                    </div>
                    
                    {item.description && (
                      <p className="text-sm text-gray-600 mb-2">{item.description}</p>
                    )}
                    
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <span>Source: {item.source}</span>
                      {item.dueDate && (
                        <span>Due: {item.dueDate.toLocaleDateString()}</span>
                      )}
                      <div className="flex space-x-1">
                        {item.tags.map((tag, index) => (
                          <span key={index} className="px-2 py-1 bg-gray-200 text-gray-700 rounded-full text-xs">
                            {tag}
                          </span>
                        ))}
                      </div>
                    </div>
                  </div>
                  
                  {item.url && (
                    <a
                      href={item.url}
                      target="_blank"
                      rel="noopener noreferrer"
                      className="text-blue-500 hover:text-blue-700"
                    >
                      <ExternalLink className="w-4 h-4" />
                    </a>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Sync Settings */}
      <div className="mt-6 p-4 bg-gray-50 rounded-lg">
        <div className="flex items-center space-x-2 mb-3">
          <Settings className="w-5 h-5 text-gray-600" />
          <h4 className="font-medium text-gray-900">Sync Settings</h4>
        </div>
        
        <div className="space-y-3">
          <label className="flex items-center space-x-3">
            <input type="checkbox" defaultChecked className="rounded" />
            <span className="text-sm text-gray-700">Auto-sync every 30 minutes</span>
          </label>
          
          <label className="flex items-center space-x-3">
            <input type="checkbox" defaultChecked className="rounded" />
            <span className="text-sm text-gray-700">Convert completed items to focus blocks</span>
          </label>
          
          <label className="flex items-center space-x-3">
            <input type="checkbox" className="rounded" />
            <span className="text-sm text-gray-700">Sync only high-priority items</span>
          </label>
          
          <label className="flex items-center space-x-3">
            <input type="checkbox" defaultChecked className="rounded" />
            <span className="text-sm text-gray-700">Preserve original due dates</span>
          </label>
        </div>
      </div>
    </div>
  );
};

export default ProductivityAppSync;
