import React, { useState, useEffect } from 'react';
import { Toaster } from 'react-hot-toast';
import { useStore } from './store/useStore';
import Header from './components/Header';
import DailyTower from './components/DailyTower';
import BlockLibrary from './components/BlockLibrary';
import FocusMode from './components/FocusMode';
import EnergyTracker from './components/EnergyTracker';
import StatsPanel from './components/StatsPanel';
import BlockTemplates from './components/BlockTemplates';
import ThemeSelector from './components/ThemeSelector';
import AIAssistant from './components/AIAssistant';
import AnalyticsDashboard from './components/AnalyticsDashboard';
import AIAutoScheduler from './components/AIAutoScheduler';
import CalendarSync from './components/CalendarSync';
import AdvancedGamification from './components/AdvancedGamification';
import SocialFeatures from './components/SocialFeatures';
import ProductivityAppSync from './components/ProductivityAppSync';
import SmartNotifications from './components/SmartNotifications';
import FreemiumModel from './components/FreemiumModel';
import CloudSync from './components/CloudSync';
import MobileNavigation from './components/MobileNavigation';
import './App.css';

function App() {
  const { isFocusMode, currentTower } = useStore();
  const [activeTab, setActiveTab] = useState<'tower' | 'templates' | 'ai' | 'analytics' | 'settings' | 'calendar' | 'gamification' | 'social' | 'sync' | 'notifications' | 'pro' | 'cloud'>('tower');
  const [isMobile, setIsMobile] = useState(false);
  const [showMobileMenu, setShowMobileMenu] = useState(false);

  const tabs = [
    { id: 'tower' as const, label: 'Tower', icon: '🏗️', shortLabel: 'Tower' },
    { id: 'templates' as const, label: 'Templates', icon: '⭐', shortLabel: 'Templates' },
    { id: 'ai' as const, label: 'AI Assistant', icon: '🤖', shortLabel: 'AI' },
    { id: 'analytics' as const, label: 'Analytics', icon: '📊', shortLabel: 'Stats' },
    { id: 'calendar' as const, label: 'Calendar', icon: '📅', shortLabel: 'Calendar' },
    { id: 'gamification' as const, label: 'Gamification', icon: '🎮', shortLabel: 'Game' },
    { id: 'social' as const, label: 'Social', icon: '👥', shortLabel: 'Social' },
    { id: 'sync' as const, label: 'Sync', icon: '🔄', shortLabel: 'Sync' },
    { id: 'notifications' as const, label: 'Notifications', icon: '🔔', shortLabel: 'Alerts' },
    { id: 'pro' as const, label: 'Pro', icon: '👑', shortLabel: 'Pro' },
    { id: 'cloud' as const, label: 'Cloud', icon: '☁️', shortLabel: 'Cloud' },
    { id: 'settings' as const, label: 'Settings', icon: '⚙️', shortLabel: 'Settings' },
  ];

  // Detect mobile device
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768);
    };
    
    checkMobile();
    window.addEventListener('resize', checkMobile);
    return () => window.removeEventListener('resize', checkMobile);
  }, []);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <Toaster position="top-right" />
      
      {isFocusMode ? (
        <FocusMode />
      ) : (
        <div className="container mx-auto px-4 py-6 pb-20 md:pb-6">
          <Header isMobile={isMobile} onMenuToggle={() => setShowMobileMenu(!showMobileMenu)} />
          
          {/* Desktop Tab Navigation */}
          {!isMobile && (
            <div className="bg-white rounded-xl shadow-lg p-2 mb-6">
              <div className="flex space-x-1">
                {tabs.map((tab) => (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id as any)}
                    className={`flex-1 flex items-center justify-center space-x-2 px-4 py-3 rounded-lg transition-colors ${
                      activeTab === tab.id
                        ? 'bg-primary-500 text-white'
                        : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100'
                    }`}
                  >
                    <span className="text-lg">{tab.icon}</span>
                    <span className="font-medium">{tab.label}</span>
                  </button>
                ))}
              </div>
            </div>
          )}

          {/* Mobile Menu Overlay */}
          {isMobile && showMobileMenu && (
            <div className="fixed inset-0 bg-black bg-opacity-50 z-50 md:hidden">
              <div className="absolute right-0 top-0 h-full w-80 bg-white shadow-xl">
                <div className="p-6">
                  <div className="flex items-center justify-between mb-6">
                    <h2 className="text-xl font-bold text-gray-900">Menu</h2>
                    <button
                      onClick={() => setShowMobileMenu(false)}
                      className="p-2 text-gray-500 hover:text-gray-700"
                    >
                      ✕
                    </button>
                  </div>
                  <div className="space-y-2">
                    {tabs.map((tab) => (
                      <button
                        key={tab.id}
                        onClick={() => {
                          setActiveTab(tab.id as any);
                          setShowMobileMenu(false);
                        }}
                        className={`w-full flex items-center space-x-3 px-4 py-3 rounded-lg transition-colors ${
                          activeTab === tab.id
                            ? 'bg-primary-500 text-white'
                            : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100'
                        }`}
                      >
                        <span className="text-xl">{tab.icon}</span>
                        <span className="font-medium">{tab.label}</span>
                      </button>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Tab Content */}
          <div className="space-y-6">
            {activeTab === 'tower' && (
              <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
                {/* Main Tower Area */}
                <div className="lg:col-span-2">
                  <DailyTower />
                </div>
                
                {/* Side Panels - Hidden on mobile, shown on desktop */}
                <div className="hidden lg:block space-y-6">
                  <EnergyTracker />
                  <StatsPanel />
                </div>
                
                {/* Block Library */}
                <div className="lg:col-span-4">
                  <BlockLibrary />
                </div>

                {/* Mobile Side Panels - Shown below main content on mobile */}
                <div className="lg:hidden grid grid-cols-1 md:grid-cols-2 gap-6">
                  <EnergyTracker />
                  <StatsPanel />
                </div>
              </div>
            )}

            {activeTab === 'templates' && (
              <BlockTemplates />
            )}

            {activeTab === 'ai' && (
              <AIAssistant />
            )}

            {activeTab === 'analytics' && (
              <AnalyticsDashboard />
            )}

            {activeTab === 'calendar' && (
              <div className="space-y-6">
                <CalendarSync 
                  isConnected={false} 
                  onConnect={() => console.log('Connect calendar')}
                  onDisconnect={() => console.log('Disconnect calendar')}
                />
                <AIAutoScheduler />
              </div>
            )}

            {activeTab === 'gamification' && (
              <AdvancedGamification />
            )}

            {activeTab === 'social' && (
              <SocialFeatures />
            )}

            {activeTab === 'sync' && (
              <ProductivityAppSync />
            )}

            {activeTab === 'notifications' && (
              <SmartNotifications />
            )}

            {activeTab === 'pro' && (
              <FreemiumModel />
            )}

            {activeTab === 'cloud' && (
              <CloudSync />
            )}

            {activeTab === 'settings' && (
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <ThemeSelector />
                <div className="bg-white rounded-xl shadow-lg p-6">
                  <h3 className="text-lg font-semibold text-gray-900 mb-4">Preferences</h3>
                  <div className="space-y-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Work Hours
                      </label>
                      <div className="flex space-x-2">
                        <input
                          type="time"
                          defaultValue="09:00"
                          className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                        />
                        <span className="flex items-center text-gray-500">to</span>
                        <input
                          type="time"
                          defaultValue="17:00"
                          className="px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                        />
                      </div>
                    </div>
                    
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Break Duration (minutes)
                      </label>
                      <input
                        type="number"
                        defaultValue="15"
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                      />
                    </div>
                    
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Pomodoro Duration (minutes)
                      </label>
                      <input
                        type="number"
                        defaultValue="25"
                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                      />
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Mobile Bottom Navigation */}
          {isMobile && (
            <MobileNavigation
              activeTab={activeTab}
              onTabChange={setActiveTab}
              tabs={tabs}
            />
          )}
        </div>
      )}
    </div>
  );
}

export default App;