import React, { useState, useEffect } from 'react';
import { Calendar, RefreshCw, CheckCircle, AlertCircle, Settings, ExternalLink } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock, BlockCategory, Priority, EnergyLevel } from '../types';

interface CalendarEvent {
  id: string;
  title: string;
  start: Date;
  end: Date;
  description?: string;
  source: 'google' | 'outlook' | 'apple';
  attendees?: string[];
  location?: string;
}

interface CalendarSyncProps {
  isConnected: boolean;
  onConnect: () => void;
  onDisconnect: () => void;
}

const CalendarSync: React.FC<CalendarSyncProps> = ({ isConnected, onConnect, onDisconnect }) => {
  const { addBlock, blocks } = useStore();
  const [events, setEvents] = useState<CalendarEvent[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [lastSync, setLastSync] = useState<Date | null>(null);
  const [syncStatus, setSyncStatus] = useState<'idle' | 'syncing' | 'success' | 'error'>('idle');

  // Mock calendar events (in real app, this would come from API)
  const mockEvents: CalendarEvent[] = [
    {
      id: '1',
      title: 'Team Standup',
      start: new Date(2024, 0, 15, 9, 0),
      end: new Date(2024, 0, 15, 9, 30),
      description: 'Daily team standup meeting',
      source: 'google',
      attendees: ['john@company.com', 'jane@company.com'],
    },
    {
      id: '2',
      title: 'Client Presentation',
      start: new Date(2024, 0, 15, 14, 0),
      end: new Date(2024, 0, 15, 15, 0),
      description: 'Present Q1 results to client',
      source: 'outlook',
      location: 'Conference Room A',
    },
    {
      id: '3',
      title: 'Gym Session',
      start: new Date(2024, 0, 15, 18, 0),
      end: new Date(2024, 0, 15, 19, 0),
      description: 'Personal workout',
      source: 'apple',
    },
  ];

  // Convert calendar event to FocusBlock
  const convertEventToBlock = (event: CalendarEvent): TimeBlock => {
    const duration = Math.round((event.end.getTime() - event.start.getTime()) / (1000 * 60));
    
    // Determine category based on event title and description
    let category: BlockCategory = 'meeting';
    if (event.title.toLowerCase().includes('gym') || event.title.toLowerCase().includes('workout')) {
      category = 'exercise';
    } else if (event.title.toLowerCase().includes('study') || event.title.toLowerCase().includes('learn')) {
      category = 'study';
    } else if (event.title.toLowerCase().includes('break') || event.title.toLowerCase().includes('lunch')) {
      category = 'break';
    } else if (event.title.toLowerCase().includes('personal') || event.title.toLowerCase().includes('family')) {
      category = 'personal';
    }

    // Determine priority based on attendees and description
    let priority: Priority = 'medium';
    if (event.attendees && event.attendees.length > 5) {
      priority = 'high';
    } else if (event.title.toLowerCase().includes('urgent') || event.title.toLowerCase().includes('important')) {
      priority = 'urgent';
    } else if (event.title.toLowerCase().includes('optional') || event.title.toLowerCase().includes('casual')) {
      priority = 'low';
    }

    // Determine energy level based on time of day
    let energyLevel: EnergyLevel = 'medium';
    const hour = event.start.getHours();
    if (hour >= 9 && hour <= 11) {
      energyLevel = 'high';
    } else if (hour >= 14 && hour <= 16) {
      energyLevel = 'medium';
    } else if (hour >= 18) {
      energyLevel = 'low';
    }

    return {
      id: `calendar-${event.id}`,
      title: event.title,
      description: event.description || `Imported from ${event.source} calendar`,
      duration,
      category,
      priority,
      energyLevel,
      completed: false,
      createdAt: new Date(),
      scheduledFor: event.start,
      tags: ['calendar-import', event.source],
    };
  };

  // Sync calendar events
  const syncCalendar = async () => {
    setIsLoading(true);
    setSyncStatus('syncing');
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      // In real app, this would fetch from actual calendar APIs
      const newEvents = mockEvents;
      setEvents(newEvents);
      setLastSync(new Date());
      setSyncStatus('success');
      
      // Auto-convert events to blocks
      newEvents.forEach(event => {
        const block = convertEventToBlock(event);
        addBlock(block);
      });
      
    } catch (error) {
      console.error('Calendar sync failed:', error);
      setSyncStatus('error');
    } finally {
      setIsLoading(false);
    }
  };

  // Auto-sync on component mount if connected
  useEffect(() => {
    if (isConnected && events.length === 0) {
      syncCalendar();
    }
  }, [isConnected]);

  const getSyncStatusIcon = () => {
    switch (syncStatus) {
      case 'syncing': return <RefreshCw className="w-4 h-4 animate-spin" />;
      case 'success': return <CheckCircle className="w-4 h-4 text-green-500" />;
      case 'error': return <AlertCircle className="w-4 h-4 text-red-500" />;
      default: return <Calendar className="w-4 h-4" />;
    }
  };

  const getSyncStatusText = () => {
    switch (syncStatus) {
      case 'syncing': return 'Syncing...';
      case 'success': return 'Synced successfully';
      case 'error': return 'Sync failed';
      default: return 'Ready to sync';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-2">
          <Calendar className="w-6 h-6 text-blue-500" />
          <h3 className="text-lg font-semibold text-gray-900">Calendar Sync</h3>
        </div>
        
        <div className="flex items-center space-x-2">
          {isConnected ? (
            <button
              onClick={onDisconnect}
              className="px-3 py-1 bg-red-100 text-red-700 rounded-full text-sm hover:bg-red-200 transition-colors"
            >
              Disconnect
            </button>
          ) : (
            <button
              onClick={onConnect}
              className="px-3 py-1 bg-green-100 text-green-700 rounded-full text-sm hover:bg-green-200 transition-colors"
            >
              Connect
            </button>
          )}
        </div>
      </div>

      {/* Connection Status */}
      <div className="mb-6">
        <div className={`p-4 rounded-lg border-2 ${
          isConnected 
            ? 'border-green-200 bg-green-50' 
            : 'border-gray-200 bg-gray-50'
        }`}>
          <div className="flex items-center space-x-3">
            {getSyncStatusIcon()}
            <div>
              <div className="font-medium text-gray-900">
                {isConnected ? 'Calendar Connected' : 'Not Connected'}
              </div>
              <div className="text-sm text-gray-600">
                {isConnected ? getSyncStatusText() : 'Connect your calendar to auto-import events'}
              </div>
            </div>
          </div>
          
          {lastSync && (
            <div className="mt-2 text-xs text-gray-500">
              Last synced: {lastSync.toLocaleString()}
            </div>
          )}
        </div>
      </div>

      {/* Sync Controls */}
      {isConnected && (
        <div className="space-y-4">
          <button
            onClick={syncCalendar}
            disabled={isLoading}
            className="w-full flex items-center justify-center space-x-2 px-4 py-3 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            {isLoading ? (
              <RefreshCw className="w-5 h-5 animate-spin" />
            ) : (
              <RefreshCw className="w-5 h-5" />
            )}
            <span>{isLoading ? 'Syncing...' : 'Sync Calendar'}</span>
          </button>

          {/* Calendar Providers */}
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {[
              { name: 'Google Calendar', icon: '📅', connected: true },
              { name: 'Outlook', icon: '📧', connected: false },
              { name: 'Apple Calendar', icon: '🍎', connected: false },
            ].map((provider) => (
              <div
                key={provider.name}
                className={`p-4 rounded-lg border-2 ${
                  provider.connected 
                    ? 'border-green-200 bg-green-50' 
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-center space-x-3">
                  <span className="text-2xl">{provider.icon}</span>
                  <div className="flex-1">
                    <div className="font-medium text-gray-900">{provider.name}</div>
                    <div className="text-sm text-gray-600">
                      {provider.connected ? 'Connected' : 'Not connected'}
                    </div>
                  </div>
                  {provider.connected ? (
                    <CheckCircle className="w-5 h-5 text-green-500" />
                  ) : (
                    <button className="text-blue-500 hover:text-blue-700">
                      <ExternalLink className="w-4 h-4" />
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Imported Events */}
      {events.length > 0 && (
        <div className="mt-6">
          <h4 className="font-medium text-gray-900 mb-3">Imported Events</h4>
          <div className="space-y-2">
            {events.map((event) => (
              <div
                key={event.id}
                className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
              >
                <div className="flex items-center space-x-3">
                  <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                  <div>
                    <div className="font-medium text-gray-900">{event.title}</div>
                    <div className="text-sm text-gray-600">
                      {event.start.toLocaleTimeString()} - {event.end.toLocaleTimeString()}
                    </div>
                  </div>
                </div>
                <div className="text-xs text-gray-500 capitalize">
                  {event.source}
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Settings */}
      <div className="mt-6 p-4 bg-gray-50 rounded-lg">
        <div className="flex items-center space-x-2 mb-3">
          <Settings className="w-5 h-5 text-gray-600" />
          <h4 className="font-medium text-gray-900">Sync Settings</h4>
        </div>
        
        <div className="space-y-3">
          <label className="flex items-center space-x-3">
            <input type="checkbox" defaultChecked className="rounded" />
            <span className="text-sm text-gray-700">Auto-sync every 15 minutes</span>
          </label>
          
          <label className="flex items-center space-x-3">
            <input type="checkbox" defaultChecked className="rounded" />
            <span className="text-sm text-gray-700">Convert events to focus blocks</span>
          </label>
          
          <label className="flex items-center space-x-3">
            <input type="checkbox" className="rounded" />
            <span className="text-sm text-gray-700">Include all-day events</span>
          </label>
        </div>
      </div>
    </div>
  );
};

export default CalendarSync;
