import React, { useState } from 'react';
import { Plus, Clock, Tag, Zap, Target } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock, BlockCategory, Priority, EnergyLevel } from '../types';

const BlockLibrary: React.FC = () => {
  const { addBlock, templates, addTemplate } = useStore();
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [newBlock, setNewBlock] = useState({
    title: '',
    description: '',
    duration: 25,
    category: 'work' as BlockCategory,
    priority: 'medium' as Priority,
    energyLevel: 'medium' as EnergyLevel,
    tags: [] as string[],
  });
  const [newTag, setNewTag] = useState('');

  const categories: { value: BlockCategory; label: string; icon: string }[] = [
    { value: 'work', label: 'Work', icon: '💼' },
    { value: 'study', label: 'Study', icon: '📚' },
    { value: 'exercise', label: 'Exercise', icon: '💪' },
    { value: 'break', label: 'Break', icon: '☕' },
    { value: 'personal', label: 'Personal', icon: '🏠' },
    { value: 'habit', label: 'Habit', icon: '🔄' },
    { value: 'meeting', label: 'Meeting', icon: '👥' },
    { value: 'creative', label: 'Creative', icon: '🎨' },
  ];

  const durations = [15, 25, 30, 45, 60, 90, 120];

  const handleCreateBlock = () => {
    if (!newBlock.title.trim()) return;

    const block: TimeBlock = {
      id: Date.now().toString(),
      title: newBlock.title,
      description: newBlock.description,
      duration: newBlock.duration,
      category: newBlock.category,
      priority: newBlock.priority,
      energyLevel: newBlock.energyLevel,
      completed: false,
      createdAt: new Date(),
      scheduledFor: new Date(),
      tags: newBlock.tags,
    };

    addBlock(block);
    
    // Reset form
    setNewBlock({
      title: '',
      description: '',
      duration: 25,
      category: 'work',
      priority: 'medium',
      energyLevel: 'medium',
      tags: [],
    });
    setShowCreateForm(false);
  };

  const handleAddTag = () => {
    if (newTag.trim() && !newBlock.tags.includes(newTag.trim())) {
      setNewBlock(prev => ({
        ...prev,
        tags: [...prev.tags, newTag.trim()]
      }));
      setNewTag('');
    }
  };

  const handleRemoveTag = (tagToRemove: string) => {
    setNewBlock(prev => ({
      ...prev,
      tags: prev.tags.filter(tag => tag !== tagToRemove)
    }));
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center justify-between mb-6">
        <h2 className="text-2xl font-bold text-gray-900">Block Library</h2>
        <button
          onClick={() => setShowCreateForm(!showCreateForm)}
          className="flex items-center space-x-2 px-4 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors"
        >
          <Plus className="w-5 h-5" />
          <span>Create Block</span>
        </button>
      </div>

      {/* Create Block Form */}
      {showCreateForm && (
        <div className="bg-gray-50 rounded-lg p-6 mb-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Create New Block</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {/* Title */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Block Title
              </label>
              <input
                type="text"
                value={newBlock.title}
                onChange={(e) => setNewBlock(prev => ({ ...prev, title: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                placeholder="Enter block title..."
              />
            </div>

            {/* Description */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Description (Optional)
              </label>
              <textarea
                value={newBlock.description}
                onChange={(e) => setNewBlock(prev => ({ ...prev, description: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                rows={2}
                placeholder="Add a description..."
              />
            </div>

            {/* Duration */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Duration
              </label>
              <div className="flex space-x-2">
                {durations.map(duration => (
                  <button
                    key={duration}
                    onClick={() => setNewBlock(prev => ({ ...prev, duration }))}
                    className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                      newBlock.duration === duration
                        ? 'bg-primary-500 text-white'
                        : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                    }`}
                  >
                    {duration}m
                  </button>
                ))}
              </div>
            </div>

            {/* Category */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Category
              </label>
              <select
                value={newBlock.category}
                onChange={(e) => setNewBlock(prev => ({ ...prev, category: e.target.value as BlockCategory }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                {categories.map(category => (
                  <option key={category.value} value={category.value}>
                    {category.icon} {category.label}
                  </option>
                ))}
              </select>
            </div>

            {/* Priority */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Priority
              </label>
              <select
                value={newBlock.priority}
                onChange={(e) => setNewBlock(prev => ({ ...prev, priority: e.target.value as Priority }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
                <option value="urgent">Urgent</option>
              </select>
            </div>

            {/* Energy Level */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Energy Level
              </label>
              <select
                value={newBlock.energyLevel}
                onChange={(e) => setNewBlock(prev => ({ ...prev, energyLevel: e.target.value as EnergyLevel }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
              </select>
            </div>

            {/* Tags */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Tags
              </label>
              <div className="flex flex-wrap gap-2 mb-2">
                {newBlock.tags.map((tag, index) => (
                  <span
                    key={index}
                    className="inline-flex items-center px-3 py-1 bg-primary-100 text-primary-800 text-sm rounded-full"
                  >
                    {tag}
                    <button
                      onClick={() => handleRemoveTag(tag)}
                      className="ml-2 text-primary-600 hover:text-primary-800"
                    >
                      ×
                    </button>
                  </span>
                ))}
              </div>
              <div className="flex space-x-2">
                <input
                  type="text"
                  value={newTag}
                  onChange={(e) => setNewTag(e.target.value)}
                  onKeyPress={(e) => e.key === 'Enter' && handleAddTag()}
                  className="flex-1 px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  placeholder="Add a tag..."
                />
                <button
                  onClick={handleAddTag}
                  className="px-4 py-2 bg-gray-200 text-gray-700 rounded-lg hover:bg-gray-300 transition-colors"
                >
                  Add
                </button>
              </div>
            </div>
          </div>

          {/* Actions */}
          <div className="flex justify-end space-x-3 mt-6">
            <button
              onClick={() => setShowCreateForm(false)}
              className="px-4 py-2 text-gray-600 hover:text-gray-800 transition-colors"
            >
              Cancel
            </button>
            <button
              onClick={handleCreateBlock}
              disabled={!newBlock.title.trim()}
              className="px-6 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              Create Block
            </button>
          </div>
        </div>
      )}

      {/* Quick Templates */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Quick Templates</h3>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
          {[
            { title: 'Pomodoro Focus', duration: 25, category: 'work' as BlockCategory, icon: '🍅' },
            { title: 'Deep Work', duration: 90, category: 'work' as BlockCategory, icon: '🧠' },
            { title: 'Exercise', duration: 30, category: 'exercise' as BlockCategory, icon: '💪' },
            { title: 'Study Session', duration: 45, category: 'study' as BlockCategory, icon: '📚' },
            { title: 'Coffee Break', duration: 15, category: 'break' as BlockCategory, icon: '☕' },
            { title: 'Creative Time', duration: 60, category: 'creative' as BlockCategory, icon: '🎨' },
            { title: 'Meeting', duration: 30, category: 'meeting' as BlockCategory, icon: '👥' },
            { title: 'Habit Building', duration: 20, category: 'habit' as BlockCategory, icon: '🔄' },
          ].map((template, index) => (
            <button
              key={index}
              onClick={() => {
                const block: TimeBlock = {
                  id: Date.now().toString() + index,
                  title: template.title,
                  description: `Quick ${template.title.toLowerCase()} session`,
                  duration: template.duration,
                  category: template.category,
                  priority: 'medium',
                  energyLevel: 'medium',
                  completed: false,
                  createdAt: new Date(),
                  scheduledFor: new Date(),
                  tags: ['template'],
                };
                addBlock(block);
              }}
              className="p-4 bg-gray-50 rounded-lg hover:bg-gray-100 transition-colors text-left"
            >
              <div className="text-2xl mb-2">{template.icon}</div>
              <div className="font-medium text-gray-900">{template.title}</div>
              <div className="text-sm text-gray-600">{template.duration}m</div>
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default BlockLibrary;
