import React from 'react';
import { Home, Star, Bot, BarChart3, Settings, Calendar, Gamepad2, Users, RefreshCw, Bell, Crown, Cloud } from 'lucide-react';

interface Tab {
  id: 'tower' | 'templates' | 'ai' | 'analytics' | 'settings' | 'calendar' | 'gamification' | 'social' | 'sync' | 'notifications' | 'pro' | 'cloud';
  label: string;
  icon: string;
  shortLabel: string;
}

interface MobileNavigationProps {
  activeTab: 'tower' | 'templates' | 'ai' | 'analytics' | 'settings' | 'calendar' | 'gamification' | 'social' | 'sync' | 'notifications' | 'pro' | 'cloud';
  onTabChange: (tab: 'tower' | 'templates' | 'ai' | 'analytics' | 'settings' | 'calendar' | 'gamification' | 'social' | 'sync' | 'notifications' | 'pro' | 'cloud') => void;
  tabs: Tab[];
}

const MobileNavigation: React.FC<MobileNavigationProps> = ({
  activeTab,
  onTabChange,
  tabs
}) => {
  const getIcon = (tabId: 'tower' | 'templates' | 'ai' | 'analytics' | 'settings' | 'calendar' | 'gamification' | 'social' | 'sync' | 'notifications' | 'pro' | 'cloud') => {
    switch (tabId) {
      case 'tower': return Home;
      case 'templates': return Star;
      case 'ai': return Bot;
      case 'analytics': return BarChart3;
      case 'calendar': return Calendar;
      case 'gamification': return Gamepad2;
      case 'social': return Users;
      case 'sync': return RefreshCw;
      case 'notifications': return Bell;
      case 'pro': return Crown;
      case 'cloud': return Cloud;
      case 'settings': return Settings;
      default: return Home;
    }
  };

  return (
    <div className="fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 z-40 md:hidden">
      <div className="flex">
        {tabs.map((tab) => {
          const Icon = getIcon(tab.id);
          const isActive = activeTab === tab.id;
          
          return (
            <button
              key={tab.id}
              onClick={() => onTabChange(tab.id)}
              className={`flex-1 flex flex-col items-center justify-center py-3 px-2 transition-colors ${
                isActive
                  ? 'text-primary-500 bg-primary-50'
                  : 'text-gray-500 hover:text-gray-700'
              }`}
            >
              <Icon className={`w-6 h-6 mb-1 ${isActive ? 'text-primary-500' : 'text-gray-500'}`} />
              <span className={`text-xs font-medium ${isActive ? 'text-primary-500' : 'text-gray-500'}`}>
                {tab.shortLabel}
              </span>
            </button>
          );
        })}
      </div>
    </div>
  );
};

export default MobileNavigation;
