import React from 'react';
import { Zap, TrendingUp, TrendingDown, Minus } from 'lucide-react';
import { useStore } from '../store/useStore';

const EnergyTracker: React.FC = () => {
  const { currentTower, logEnergyLevel } = useStore();

  const energyLevels = [
    { level: 'high' as const, label: 'High', icon: TrendingUp, color: 'text-green-600', bgColor: 'bg-green-100' },
    { level: 'medium' as const, label: 'Medium', icon: Minus, color: 'text-yellow-600', bgColor: 'bg-yellow-100' },
    { level: 'low' as const, label: 'Low', icon: TrendingDown, color: 'text-red-600', bgColor: 'bg-red-100' },
  ];

  const getEnergyIcon = (level: string) => {
    switch (level) {
      case 'high': return '⚡';
      case 'medium': return '🔋';
      case 'low': return '🪫';
      default: return '🔋';
    }
  };

  const getEnergyColor = (level: string) => {
    switch (level) {
      case 'high': return 'energy-high';
      case 'medium': return 'energy-medium';
      case 'low': return 'energy-low';
      default: return 'energy-medium';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-4">
        <Zap className="w-6 h-6 text-yellow-500" />
        <h3 className="text-lg font-semibold text-gray-900">Energy Level</h3>
      </div>

      {/* Current Energy Display */}
      <div className="text-center mb-6">
        <div className="text-4xl mb-2">
          {getEnergyIcon(currentTower?.energyLevel || 'medium')}
        </div>
        <div className={`text-2xl font-bold ${getEnergyColor(currentTower?.energyLevel || 'medium')} text-white px-4 py-2 rounded-lg`}>
          {currentTower?.energyLevel?.toUpperCase() || 'MEDIUM'} ENERGY
        </div>
        <p className="text-sm text-gray-600 mt-2">
          How energized do you feel right now?
        </p>
      </div>

      {/* Energy Selection */}
      <div className="space-y-3">
        {energyLevels.map(({ level, label, icon: Icon, color, bgColor }) => (
          <button
            key={level}
            onClick={() => logEnergyLevel(level)}
            className={`w-full flex items-center justify-between p-3 rounded-lg transition-colors ${
              currentTower?.energyLevel === level
                ? `${bgColor} ${color} ring-2 ring-current`
                : 'bg-gray-50 hover:bg-gray-100 text-gray-700'
            }`}
          >
            <div className="flex items-center space-x-3">
              <Icon className="w-5 h-5" />
              <span className="font-medium">{label}</span>
            </div>
            <div className="text-2xl">
              {getEnergyIcon(level)}
            </div>
          </button>
        ))}
      </div>

      {/* Energy Tips */}
      <div className="mt-6 p-4 bg-blue-50 rounded-lg">
        <h4 className="font-medium text-blue-900 mb-2">Energy Tips</h4>
        <div className="text-sm text-blue-800 space-y-1">
          {currentTower?.energyLevel === 'high' && (
            <p>• Perfect for deep work and complex tasks</p>
          )}
          {currentTower?.energyLevel === 'medium' && (
            <p>• Good for regular work and meetings</p>
          )}
          {currentTower?.energyLevel === 'low' && (
            <p>• Focus on lighter tasks and breaks</p>
          )}
          <p>• Schedule demanding blocks when energy is high</p>
        </div>
      </div>
    </div>
  );
};

export default EnergyTracker;
