import { create } from 'zustand';
import { TimeBlock, DailyTower, User, UserPreferences, FocusSession, BlockTemplate } from '../types';

interface FocusBlocksStore {
  // User data
  user: User | null;
  setUser: (user: User) => void;
  
  // Daily towers
  currentTower: DailyTower | null;
  towers: DailyTower[];
  setCurrentTower: (tower: DailyTower) => void;
  addTower: (tower: DailyTower) => void;
  updateTower: (towerId: string, updates: Partial<DailyTower>) => void;
  
  // Time blocks
  blocks: TimeBlock[];
  addBlock: (block: TimeBlock) => void;
  updateBlock: (blockId: string, updates: Partial<TimeBlock>) => void;
  deleteBlock: (blockId: string) => void;
  moveBlock: (blockId: string, newTowerId: string, newIndex: number) => void;
  
  // Focus sessions
  activeFocusSession: FocusSession | null;
  startFocusSession: (blockId: string) => void;
  endFocusSession: () => void;
  
  // Templates
  templates: BlockTemplate[];
  addTemplate: (template: BlockTemplate) => void;
  updateTemplate: (templateId: string, updates: Partial<BlockTemplate>) => void;
  deleteTemplate: (templateId: string) => void;
  useTemplate: (templateId: string) => void;
  
  // UI state
  selectedDate: Date;
  setSelectedDate: (date: Date) => void;
  isFocusMode: boolean;
  setFocusMode: (isFocus: boolean) => void;
  
  // Energy tracking
  logEnergyLevel: (level: 'low' | 'medium' | 'high') => void;
  
  // Gamification
  completeBlock: (blockId: string) => void;
  getStreak: () => number;
  getLevel: () => number;
}

const defaultUser: User = {
  id: '1',
  name: 'Focus User',
  email: 'user@focusblocks.com',
  preferences: {
    workHours: { start: '09:00', end: '17:00' },
    breakDuration: 15,
    pomodoroDuration: 25,
    ambientSounds: true,
    notifications: true,
    theme: 'auto',
  },
  stats: {
    totalBlocksCompleted: 0,
    currentStreak: 0,
    longestStreak: 0,
    level: 1,
    experience: 0,
    focusTime: 0,
  },
};

export const useStore = create<FocusBlocksStore>()((set, get) => ({
      // User data
      user: defaultUser,
      setUser: (user) => set({ user }),
      
      // Daily towers
      currentTower: null,
      towers: [],
      setCurrentTower: (tower) => set({ currentTower: tower }),
      addTower: (tower) => set((state) => ({ 
        towers: [...state.towers, tower],
        currentTower: tower 
      })),
      updateTower: (towerId, updates) => set((state) => ({
        towers: state.towers.map(tower => 
          tower.id === towerId ? { ...tower, ...updates } : tower
        ),
        currentTower: state.currentTower?.id === towerId 
          ? { ...state.currentTower, ...updates }
          : state.currentTower
      })),
      
      // Time blocks
      blocks: [],
      addBlock: (block) => set((state) => ({ 
        blocks: [...state.blocks, block] 
      })),
      updateBlock: (blockId, updates) => set((state) => ({
        blocks: state.blocks.map(block => 
          block.id === blockId ? { ...block, ...updates } : block
        )
      })),
      deleteBlock: (blockId) => set((state) => ({
        blocks: state.blocks.filter(block => block.id !== blockId)
      })),
      moveBlock: (blockId, newTowerId, newIndex) => {
        // Implementation for moving blocks between towers
        const { blocks, towers } = get();
        const block = blocks.find(b => b.id === blockId);
        if (!block) return;
        
        // Update block's scheduled date
        const newTower = towers.find(t => t.id === newTowerId);
        if (newTower) {
          set((state) => ({
            blocks: state.blocks.map(b => 
              b.id === blockId ? { ...b, scheduledFor: newTower.date } : b
            )
          }));
        }
      },
      
      // Focus sessions
      activeFocusSession: null,
      startFocusSession: (blockId) => {
        const session: FocusSession = {
          id: Date.now().toString(),
          blockId,
          startTime: new Date(),
          isActive: true,
          distractions: 0,
        };
        set({ activeFocusSession: session, isFocusMode: true });
      },
      endFocusSession: () => set((state) => ({
        activeFocusSession: state.activeFocusSession ? {
          ...state.activeFocusSession,
          endTime: new Date(),
          isActive: false,
        } : null,
        isFocusMode: false,
      })),
      
      // Templates
      templates: [],
      addTemplate: (template) => set((state) => ({ 
        templates: [...state.templates, template] 
      })),
      updateTemplate: (templateId, updates) => set((state) => ({
        templates: state.templates.map(template => 
          template.id === templateId ? { ...template, ...updates } : template
        )
      })),
      deleteTemplate: (templateId) => set((state) => ({
        templates: state.templates.filter(t => t.id !== templateId)
      })),
      useTemplate: (templateId) => {
        const { templates, addBlock, selectedDate } = get();
        const template = templates.find(t => t.id === templateId);
        if (!template) return;
        
        const block: TimeBlock = {
          id: Date.now().toString(),
          title: template.title,
          description: template.description,
          duration: template.duration,
          category: template.category,
          priority: template.priority,
          energyLevel: template.energyLevel,
          completed: false,
          createdAt: new Date(),
          scheduledFor: selectedDate,
          tags: template.tags,
        };
        
        addBlock(block);
        
        // Update usage count
        set((state) => ({
          templates: state.templates.map(t => 
            t.id === templateId ? { ...t, usageCount: t.usageCount + 1 } : t
          )
        }));
      },
      
      // UI state
      selectedDate: new Date(),
      setSelectedDate: (date) => set({ selectedDate: date }),
      isFocusMode: false,
      setFocusMode: (isFocus) => set({ isFocusMode: isFocus }),
      
      // Energy tracking
      logEnergyLevel: (level) => {
        const { currentTower } = get();
        if (currentTower) {
          set((state) => ({
            currentTower: state.currentTower ? {
              ...state.currentTower,
              energyLevel: level,
            } : null
          }));
        }
      },
      
      // Gamification
      completeBlock: (blockId) => {
        const { blocks, user } = get();
        const block = blocks.find(b => b.id === blockId);
        if (!block || block.completed) return;
        
        const now = new Date();
        set((state) => ({
          blocks: state.blocks.map(b => 
            b.id === blockId ? { ...b, completed: true, completedAt: now } : b
          ),
          user: state.user ? {
            ...state.user,
            stats: {
              ...state.user.stats,
              totalBlocksCompleted: state.user.stats.totalBlocksCompleted + 1,
              experience: state.user.stats.experience + (block.duration * 10),
            }
          } : null
        }));
      },
      
      getStreak: () => {
        const { user } = get();
        return user?.stats.currentStreak || 0;
      },
      
      getLevel: () => {
        const { user } = get();
        return user?.stats.level || 1;
      },
    })
);
