import React, { useState } from 'react';
import { Plus, Edit3, Trash2, Star, Clock, Zap, Tag } from 'lucide-react';
import { useStore } from '../store/useStore';
import { BlockTemplate, BlockCategory, Priority, EnergyLevel } from '../types';

const BlockTemplates: React.FC = () => {
  const { templates, addTemplate, deleteTemplate, addBlock } = useStore();
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [editingTemplate, setEditingTemplate] = useState<string | null>(null);
  const [newTemplate, setNewTemplate] = useState({
    title: '',
    description: '',
    duration: 25,
    category: 'work' as BlockCategory,
    priority: 'medium' as Priority,
    energyLevel: 'medium' as EnergyLevel,
    isRecurring: false,
    frequency: 'daily' as 'daily' | 'weekly' | 'monthly',
    tags: [] as string[],
  });
  const [newTag, setNewTag] = useState('');

  const categories: { value: BlockCategory; label: string; icon: string; color: string }[] = [
    { value: 'work', label: 'Work', icon: '💼', color: 'from-blue-500 to-blue-600' },
    { value: 'study', label: 'Study', icon: '📚', color: 'from-purple-500 to-purple-600' },
    { value: 'exercise', label: 'Exercise', icon: '💪', color: 'from-green-500 to-green-600' },
    { value: 'break', label: 'Break', icon: '☕', color: 'from-yellow-500 to-orange-500' },
    { value: 'personal', label: 'Personal', icon: '🏠', color: 'from-pink-500 to-pink-600' },
    { value: 'habit', label: 'Habit', icon: '🔄', color: 'from-indigo-500 to-indigo-600' },
    { value: 'meeting', label: 'Meeting', icon: '👥', color: 'from-cyan-500 to-cyan-600' },
    { value: 'creative', label: 'Creative', icon: '🎨', color: 'from-rose-500 to-rose-600' },
  ];

  const durations = [15, 25, 30, 45, 60, 90, 120, 180];

  const handleCreateTemplate = () => {
    if (!newTemplate.title.trim()) return;

    const template: BlockTemplate = {
      id: Date.now().toString(),
      title: newTemplate.title,
      description: newTemplate.description,
      duration: newTemplate.duration,
      category: newTemplate.category,
      priority: newTemplate.priority,
      energyLevel: newTemplate.energyLevel,
      isRecurring: newTemplate.isRecurring,
      frequency: newTemplate.frequency,
      tags: newTemplate.tags,
      createdAt: new Date(),
      usageCount: 0,
    };

    addTemplate(template);
    
    // Reset form
    setNewTemplate({
      title: '',
      description: '',
      duration: 25,
      category: 'work',
      priority: 'medium',
      energyLevel: 'medium',
      isRecurring: false,
      frequency: 'daily',
      tags: [],
    });
    setShowCreateForm(false);
  };

  const handleUseTemplate = (template: BlockTemplate) => {
    const block = {
      id: Date.now().toString(),
      title: template.title,
      description: template.description,
      duration: template.duration,
      category: template.category,
      priority: template.priority,
      energyLevel: template.energyLevel,
      completed: false,
      createdAt: new Date(),
      scheduledFor: new Date(),
      tags: [],
    };

    addBlock(block);
  };

  const handleAddTag = () => {
    if (newTag.trim() && !newTemplate.tags.includes(newTag.trim())) {
      setNewTemplate(prev => ({
        ...prev,
        tags: [...prev.tags, newTag.trim()]
      }));
      setNewTag('');
    }
  };

  const handleRemoveTag = (tagToRemove: string) => {
    setNewTemplate(prev => ({
      ...prev,
      tags: prev.tags.filter(tag => tag !== tagToRemove)
    }));
  };

  const getCategoryInfo = (category: BlockCategory) => {
    return categories.find(cat => cat.value === category) || categories[0];
  };

  const formatDuration = (minutes: number) => {
    if (minutes < 60) return `${minutes}m`;
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    return mins > 0 ? `${hours}h ${mins}m` : `${hours}h`;
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-2">
          <Star className="w-6 h-6 text-yellow-500" />
          <h2 className="text-2xl font-bold text-gray-900">Block Templates</h2>
        </div>
        <button
          onClick={() => setShowCreateForm(!showCreateForm)}
          className="flex items-center space-x-2 px-4 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors"
        >
          <Plus className="w-5 h-5" />
          <span>Create Template</span>
        </button>
      </div>

      {/* Create Template Form */}
      {showCreateForm && (
        <div className="bg-gray-50 rounded-lg p-6 mb-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Create New Template</h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {/* Title */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Template Title
              </label>
              <input
                type="text"
                value={newTemplate.title}
                onChange={(e) => setNewTemplate(prev => ({ ...prev, title: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                placeholder="Enter template title..."
              />
            </div>

            {/* Description */}
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Description (Optional)
              </label>
              <textarea
                value={newTemplate.description}
                onChange={(e) => setNewTemplate(prev => ({ ...prev, description: e.target.value }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                rows={2}
                placeholder="Add a description..."
              />
            </div>

            {/* Duration */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Duration
              </label>
              <div className="flex flex-wrap gap-2">
                {durations.map(duration => (
                  <button
                    key={duration}
                    onClick={() => setNewTemplate(prev => ({ ...prev, duration }))}
                    className={`px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                      newTemplate.duration === duration
                        ? 'bg-primary-500 text-white'
                        : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                    }`}
                  >
                    {formatDuration(duration)}
                  </button>
                ))}
              </div>
            </div>

            {/* Category */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Category
              </label>
              <select
                value={newTemplate.category}
                onChange={(e) => setNewTemplate(prev => ({ ...prev, category: e.target.value as BlockCategory }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                {categories.map(category => (
                  <option key={category.value} value={category.value}>
                    {category.icon} {category.label}
                  </option>
                ))}
              </select>
            </div>

            {/* Priority */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Priority
              </label>
              <select
                value={newTemplate.priority}
                onChange={(e) => setNewTemplate(prev => ({ ...prev, priority: e.target.value as Priority }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
                <option value="urgent">Urgent</option>
              </select>
            </div>

            {/* Energy Level */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                Energy Level
              </label>
              <select
                value={newTemplate.energyLevel}
                onChange={(e) => setNewTemplate(prev => ({ ...prev, energyLevel: e.target.value as EnergyLevel }))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="low">Low</option>
                <option value="medium">Medium</option>
                <option value="high">High</option>
              </select>
            </div>

            {/* Recurring */}
            <div className="md:col-span-2">
              <label className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={newTemplate.isRecurring}
                  onChange={(e) => setNewTemplate(prev => ({ ...prev, isRecurring: e.target.checked }))}
                  className="rounded"
                />
                <span className="text-sm font-medium text-gray-700">Recurring Template</span>
              </label>
              
              {newTemplate.isRecurring && (
                <select
                  value={newTemplate.frequency}
                  onChange={(e) => setNewTemplate(prev => ({ ...prev, frequency: e.target.value as 'daily' | 'weekly' | 'monthly' }))}
                  className="mt-2 w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                >
                  <option value="daily">Daily</option>
                  <option value="weekly">Weekly</option>
                  <option value="monthly">Monthly</option>
                </select>
              )}
            </div>
          </div>

          {/* Actions */}
          <div className="flex justify-end space-x-3 mt-6">
            <button
              onClick={() => setShowCreateForm(false)}
              className="px-4 py-2 text-gray-600 hover:text-gray-800 transition-colors"
            >
              Cancel
            </button>
            <button
              onClick={handleCreateTemplate}
              disabled={!newTemplate.title.trim()}
              className="px-6 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
            >
              Create Template
            </button>
          </div>
        </div>
      )}

      {/* Templates Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {templates.map((template) => {
          const categoryInfo = getCategoryInfo(template.category);
          return (
            <div
              key={template.id}
              className={`p-4 rounded-lg border-2 border-transparent hover:border-primary-200 transition-all cursor-pointer group ${categoryInfo.color} bg-gradient-to-r text-white`}
            >
              <div className="flex items-start justify-between mb-3">
                <div className="flex items-center space-x-2">
                  <span className="text-2xl">{categoryInfo.icon}</span>
                  <div>
                    <h3 className="font-semibold">{template.title}</h3>
                    {template.description && (
                      <p className="text-sm opacity-90">{template.description}</p>
                    )}
                  </div>
                </div>
                
                <div className="flex items-center space-x-1">
                  <button
                    onClick={() => handleUseTemplate(template)}
                    className="p-1 hover:bg-white/20 rounded transition-colors"
                    title="Use Template"
                  >
                    <Plus className="w-4 h-4" />
                  </button>
                  <button
                    onClick={() => deleteTemplate(template.id)}
                    className="p-1 hover:bg-white/20 rounded transition-colors"
                    title="Delete Template"
                  >
                    <Trash2 className="w-4 h-4" />
                  </button>
                </div>
              </div>

              <div className="flex items-center justify-between text-sm">
                <div className="flex items-center space-x-3">
                  <div className="flex items-center space-x-1">
                    <Clock className="w-4 h-4" />
                    <span>{formatDuration(template.duration)}</span>
                  </div>
                  <div className="flex items-center space-x-1">
                    <Zap className="w-4 h-4" />
                    <span className="capitalize">{template.energyLevel}</span>
                  </div>
                </div>
                
                {template.isRecurring && (
                  <div className="flex items-center space-x-1">
                    <Star className="w-4 h-4" />
                    <span className="capitalize">{template.frequency}</span>
                  </div>
                )}
              </div>
            </div>
          );
        })}

        {templates.length === 0 && (
          <div className="col-span-full text-center py-12 text-gray-500">
            <Star className="w-16 h-16 mx-auto mb-4 opacity-50" />
            <h3 className="text-xl font-medium mb-2">No Templates Yet</h3>
            <p className="max-w-sm mx-auto">
              Create reusable block templates to quickly add common tasks to your daily tower.
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default BlockTemplates;
