import React, { useState, useEffect } from 'react';
import { Trophy, Star, Gift, Crown, Zap, Target, Users, Coins } from 'lucide-react';
import { useStore } from '../store/useStore';

interface Achievement {
  id: string;
  title: string;
  description: string;
  icon: string;
  unlocked: boolean;
  progress: number;
  maxProgress: number;
  reward: {
    coins: number;
    xp: number;
    badge?: string;
  };
  category: 'streak' | 'focus' | 'social' | 'challenge' | 'special';
}

interface Reward {
  id: string;
  name: string;
  description: string;
  cost: number;
  type: 'theme' | 'avatar' | 'sound' | 'animation';
  unlocked: boolean;
  icon: string;
}

interface Challenge {
  id: string;
  title: string;
  description: string;
  type: 'daily' | 'weekly' | 'monthly';
  target: number;
  current: number;
  reward: number;
  deadline: Date;
  completed: boolean;
}

const AdvancedGamification: React.FC = () => {
  const { user, blocks } = useStore();
  const [activeTab, setActiveTab] = useState<'achievements' | 'rewards' | 'challenges' | 'leaderboard'>('achievements');
  const [coins, setCoins] = useState(1250);
  const [level, setLevel] = useState(12);
  const [xp, setXp] = useState(3400);
  const [xpToNext, setXpToNext] = useState(600);

  // Mock achievements data
  const [achievements, setAchievements] = useState<Achievement[]>([
    {
      id: 'first-block',
      title: 'First Steps',
      description: 'Complete your first focus block',
      icon: '🎯',
      unlocked: true,
      progress: 1,
      maxProgress: 1,
      reward: { coins: 50, xp: 100 },
      category: 'focus'
    },
    {
      id: 'streak-master',
      title: 'Streak Master',
      description: 'Maintain a 7-day streak',
      icon: '🔥',
      unlocked: false,
      progress: 5,
      maxProgress: 7,
      reward: { coins: 200, xp: 500 },
      category: 'streak'
    },
    {
      id: 'focus-warrior',
      title: 'Focus Warrior',
      description: 'Complete 100 focus blocks',
      icon: '⚔️',
      unlocked: false,
      progress: 45,
      maxProgress: 100,
      reward: { coins: 500, xp: 1000 },
      category: 'focus'
    },
    {
      id: 'time-master',
      title: 'Time Master',
      description: 'Log 1000 minutes of focus time',
      icon: '⏰',
      unlocked: false,
      progress: 720,
      maxProgress: 1000,
      reward: { coins: 300, xp: 750 },
      category: 'focus'
    },
    {
      id: 'early-bird',
      title: 'Early Bird',
      description: 'Complete 10 morning blocks (6-9 AM)',
      icon: '🐦',
      unlocked: false,
      progress: 3,
      maxProgress: 10,
      reward: { coins: 150, xp: 300 },
      category: 'special'
    },
    {
      id: 'team-player',
      title: 'Team Player',
      description: 'Join 5 focus sessions with others',
      icon: '👥',
      unlocked: false,
      progress: 2,
      maxProgress: 5,
      reward: { coins: 250, xp: 400 },
      category: 'social'
    }
  ]);

  // Mock rewards store
  const [rewards, setRewards] = useState<Reward[]>([
    {
      id: 'theme-dark',
      name: 'Dark Theme',
      description: 'Sleek dark theme for night owls',
      cost: 200,
      type: 'theme',
      unlocked: false,
      icon: '🌙'
    },
    {
      id: 'theme-gamer',
      name: 'Gamer Theme',
      description: 'Bold colors for gaming enthusiasts',
      cost: 300,
      type: 'theme',
      unlocked: false,
      icon: '🎮'
    },
    {
      id: 'avatar-robot',
      name: 'Robot Avatar',
      description: 'Futuristic robot companion',
      cost: 500,
      type: 'avatar',
      unlocked: false,
      icon: '🤖'
    },
    {
      id: 'sound-rain',
      name: 'Rain Sounds',
      description: 'Calming rain for focus sessions',
      cost: 150,
      type: 'sound',
      unlocked: false,
      icon: '🌧️'
    },
    {
      id: 'animation-sparkles',
      name: 'Sparkle Animation',
      description: 'Magical sparkles on block completion',
      cost: 100,
      type: 'animation',
      unlocked: false,
      icon: '✨'
    }
  ]);

  // Mock challenges
  const [challenges, setChallenges] = useState<Challenge[]>([
    {
      id: 'daily-focus',
      title: 'Daily Focus',
      description: 'Complete 5 focus blocks today',
      type: 'daily',
      target: 5,
      current: 3,
      reward: 100,
      deadline: new Date(Date.now() + 24 * 60 * 60 * 1000),
      completed: false
    },
    {
      id: 'weekly-streak',
      title: 'Weekly Warrior',
      description: 'Maintain a 5-day streak this week',
      type: 'weekly',
      target: 5,
      current: 3,
      reward: 500,
      deadline: new Date(Date.now() + 7 * 24 * 60 * 60 * 1000),
      completed: false
    },
    {
      id: 'monthly-master',
      title: 'Monthly Master',
      description: 'Complete 200 focus blocks this month',
      type: 'monthly',
      target: 200,
      current: 45,
      reward: 2000,
      deadline: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000),
      completed: false
    }
  ]);

  // Mock leaderboard data
  const leaderboard = [
    { rank: 1, name: 'FocusMaster', level: 25, xp: 15000, avatar: '👑' },
    { rank: 2, name: 'ProductivityPro', level: 23, xp: 14200, avatar: '⚡' },
    { rank: 3, name: 'TimeWizard', level: 22, xp: 13800, avatar: '🧙' },
    { rank: 4, name: 'BlockBuilder', level: 21, xp: 13200, avatar: '🏗️' },
    { rank: 5, name: 'FocusNinja', level: 20, xp: 12800, avatar: '🥷' }
  ];

  const purchaseReward = (rewardId: string) => {
    const reward = rewards.find(r => r.id === rewardId);
    if (reward && coins >= reward.cost) {
      setCoins(coins - reward.cost);
      setRewards(rewards.map(r => 
        r.id === rewardId ? { ...r, unlocked: true } : r
      ));
    }
  };

  const claimAchievement = (achievementId: string) => {
    const achievement = achievements.find(a => a.id === achievementId);
    if (achievement && achievement.unlocked) {
      setCoins(coins + achievement.reward.coins);
      setXp(xp + achievement.reward.xp);
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'streak': return 'text-orange-600 bg-orange-100';
      case 'focus': return 'text-blue-600 bg-blue-100';
      case 'social': return 'text-green-600 bg-green-100';
      case 'challenge': return 'text-purple-600 bg-purple-100';
      case 'special': return 'text-pink-600 bg-pink-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getLevelProgress = () => {
    return (xp / (xp + xpToNext)) * 100;
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      {/* Header with Stats */}
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center space-x-2">
          <Crown className="w-6 h-6 text-yellow-500" />
          <h3 className="text-lg font-semibold text-gray-900">Gamification Hub</h3>
        </div>
        
        <div className="flex items-center space-x-4">
          <div className="flex items-center space-x-1">
            <Coins className="w-5 h-5 text-yellow-500" />
            <span className="font-bold text-gray-900">{coins}</span>
          </div>
          <div className="flex items-center space-x-1">
            <Zap className="w-5 h-5 text-blue-500" />
            <span className="font-bold text-gray-900">Level {level}</span>
          </div>
        </div>
      </div>

      {/* Level Progress */}
      <div className="mb-6">
        <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
          <span>Level Progress</span>
          <span>{xp} / {xp + xpToNext} XP</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-3">
          <div 
            className="bg-gradient-to-r from-blue-500 to-purple-500 h-3 rounded-full transition-all duration-500"
            style={{ width: `${getLevelProgress()}%` }}
          />
        </div>
      </div>

      {/* Tab Navigation */}
      <div className="flex space-x-1 mb-6 bg-gray-100 rounded-lg p-1">
        {[
          { id: 'achievements', label: 'Achievements', icon: Trophy },
          { id: 'rewards', label: 'Rewards', icon: Gift },
          { id: 'challenges', label: 'Challenges', icon: Target },
          { id: 'leaderboard', label: 'Leaderboard', icon: Users },
        ].map((tab) => {
          const Icon = tab.icon;
          return (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`flex-1 flex items-center justify-center space-x-2 px-3 py-2 rounded-md transition-colors ${
                activeTab === tab.id
                  ? 'bg-white text-blue-600 shadow-sm'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              <Icon className="w-4 h-4" />
              <span className="text-sm font-medium">{tab.label}</span>
            </button>
          );
        })}
      </div>

      {/* Tab Content */}
      <div className="space-y-4">
        {activeTab === 'achievements' && (
          <div className="space-y-3">
            {achievements.map((achievement) => (
              <div
                key={achievement.id}
                className={`p-4 rounded-lg border-2 ${
                  achievement.unlocked 
                    ? 'border-green-200 bg-green-50' 
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-start space-x-3">
                  <div className="text-3xl">{achievement.icon}</div>
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <h4 className="font-medium text-gray-900">{achievement.title}</h4>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getCategoryColor(achievement.category)}`}>
                        {achievement.category}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mb-2">{achievement.description}</p>
                    
                    <div className="flex items-center justify-between">
                      <div className="flex-1 mr-4">
                        <div className="flex items-center justify-between text-xs text-gray-600 mb-1">
                          <span>Progress</span>
                          <span>{achievement.progress} / {achievement.maxProgress}</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2">
                          <div 
                            className="bg-gradient-to-r from-blue-500 to-purple-500 h-2 rounded-full transition-all duration-300"
                            style={{ width: `${(achievement.progress / achievement.maxProgress) * 100}%` }}
                          />
                        </div>
                      </div>
                      
                      <div className="flex items-center space-x-2">
                        <div className="text-right">
                          <div className="text-sm font-medium text-yellow-600">
                            +{achievement.reward.coins} coins
                          </div>
                          <div className="text-xs text-blue-600">
                            +{achievement.reward.xp} XP
                          </div>
                        </div>
                        {achievement.unlocked ? (
                          <button
                            onClick={() => claimAchievement(achievement.id)}
                            className="px-3 py-1 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors text-sm"
                          >
                            Claim
                          </button>
                        ) : (
                          <div className="px-3 py-1 bg-gray-300 text-gray-600 rounded-lg text-sm">
                            Locked
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'rewards' && (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {rewards.map((reward) => (
              <div
                key={reward.id}
                className={`p-4 rounded-lg border-2 ${
                  reward.unlocked 
                    ? 'border-green-200 bg-green-50' 
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-center space-x-3 mb-3">
                  <div className="text-2xl">{reward.icon}</div>
                  <div className="flex-1">
                    <h4 className="font-medium text-gray-900">{reward.name}</h4>
                    <p className="text-sm text-gray-600">{reward.description}</p>
                  </div>
                </div>
                
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-1">
                    <Coins className="w-4 h-4 text-yellow-500" />
                    <span className="font-medium text-gray-900">{reward.cost}</span>
                  </div>
                  
                  {reward.unlocked ? (
                    <div className="px-3 py-1 bg-green-500 text-white rounded-lg text-sm">
                      Owned
                    </div>
                  ) : (
                    <button
                      onClick={() => purchaseReward(reward.id)}
                      disabled={coins < reward.cost}
                      className="px-3 py-1 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                    >
                      Buy
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'challenges' && (
          <div className="space-y-4">
            {challenges.map((challenge) => (
              <div
                key={challenge.id}
                className={`p-4 rounded-lg border-2 ${
                  challenge.completed 
                    ? 'border-green-200 bg-green-50' 
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <h4 className="font-medium text-gray-900">{challenge.title}</h4>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        challenge.type === 'daily' ? 'text-blue-600 bg-blue-100' :
                        challenge.type === 'weekly' ? 'text-purple-600 bg-purple-100' :
                        'text-orange-600 bg-orange-100'
                      }`}>
                        {challenge.type}
                      </span>
                    </div>
                    <p className="text-sm text-gray-600 mb-3">{challenge.description}</p>
                    
                    <div className="flex items-center justify-between">
                      <div className="flex-1 mr-4">
                        <div className="flex items-center justify-between text-xs text-gray-600 mb-1">
                          <span>Progress</span>
                          <span>{challenge.current} / {challenge.target}</span>
                        </div>
                        <div className="w-full bg-gray-200 rounded-full h-2">
                          <div 
                            className="bg-gradient-to-r from-blue-500 to-purple-500 h-2 rounded-full transition-all duration-300"
                            style={{ width: `${(challenge.current / challenge.target) * 100}%` }}
                          />
                        </div>
                      </div>
                      
                      <div className="flex items-center space-x-2">
                        <div className="text-right">
                          <div className="text-sm font-medium text-yellow-600">
                            +{challenge.reward} coins
                          </div>
                          <div className="text-xs text-gray-500">
                            Due: {challenge.deadline.toLocaleDateString()}
                          </div>
                        </div>
                        {challenge.completed ? (
                          <div className="px-3 py-1 bg-green-500 text-white rounded-lg text-sm">
                            Completed
                          </div>
                        ) : (
                          <div className="px-3 py-1 bg-gray-300 text-gray-600 rounded-lg text-sm">
                            In Progress
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'leaderboard' && (
          <div className="space-y-3">
            {leaderboard.map((player) => (
              <div
                key={player.rank}
                className={`p-4 rounded-lg ${
                  player.rank <= 3 
                    ? 'bg-gradient-to-r from-yellow-50 to-orange-50 border-2 border-yellow-200' 
                    : 'bg-gray-50 border border-gray-200'
                }`}
              >
                <div className="flex items-center space-x-4">
                  <div className={`w-8 h-8 rounded-full flex items-center justify-center font-bold ${
                    player.rank === 1 ? 'bg-yellow-500 text-white' :
                    player.rank === 2 ? 'bg-gray-400 text-white' :
                    player.rank === 3 ? 'bg-orange-500 text-white' :
                    'bg-gray-300 text-gray-700'
                  }`}>
                    {player.rank}
                  </div>
                  
                  <div className="text-2xl">{player.avatar}</div>
                  
                  <div className="flex-1">
                    <div className="font-medium text-gray-900">{player.name}</div>
                    <div className="text-sm text-gray-600">Level {player.level} • {player.xp.toLocaleString()} XP</div>
                  </div>
                  
                  {player.rank <= 3 && (
                    <div className="text-2xl">
                      {player.rank === 1 ? '🥇' : player.rank === 2 ? '🥈' : '🥉'}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default AdvancedGamification;
