import React from 'react';
import { Palette, Sun, Moon, Zap, Heart, Gamepad2, Sparkles } from 'lucide-react';
import { useStore } from '../store/useStore';

const themes = [
  {
    id: 'light',
    name: 'Light',
    description: 'Clean and bright',
    icon: Sun,
    colors: {
      primary: 'from-blue-500 to-blue-600',
      background: 'from-slate-50 to-blue-50',
      card: 'bg-white',
      text: 'text-gray-900',
    }
  },
  {
    id: 'dark',
    name: 'Dark',
    description: 'Easy on the eyes',
    icon: Moon,
    colors: {
      primary: 'from-blue-400 to-blue-500',
      background: 'from-slate-900 to-slate-800',
      card: 'bg-slate-800',
      text: 'text-white',
    }
  },
  {
    id: 'calm',
    name: 'Calm',
    description: 'Peaceful and zen',
    icon: Heart,
    colors: {
      primary: 'from-green-500 to-emerald-600',
      background: 'from-green-50 to-emerald-50',
      card: 'bg-white',
      text: 'text-gray-800',
    }
  },
  {
    id: 'minimal',
    name: 'Minimal',
    description: 'Clean and simple',
    icon: Palette,
    colors: {
      primary: 'from-gray-600 to-gray-700',
      background: 'from-gray-50 to-gray-100',
      card: 'bg-white',
      text: 'text-gray-900',
    }
  },
  {
    id: 'gamer',
    name: 'Gamer',
    description: 'Bold and energetic',
    icon: Gamepad2,
    colors: {
      primary: 'from-purple-500 to-pink-600',
      background: 'from-purple-900 to-pink-900',
      card: 'bg-slate-800',
      text: 'text-white',
    }
  },
  {
    id: 'neon',
    name: 'Neon',
    description: 'Bright and futuristic',
    icon: Sparkles,
    colors: {
      primary: 'from-cyan-400 to-pink-500',
      background: 'from-slate-900 to-purple-900',
      card: 'bg-slate-800',
      text: 'text-white',
    }
  }
];

const ThemeSelector: React.FC = () => {
  const { user, setUser } = useStore();
  const currentTheme = user?.preferences?.theme || 'light';

  const handleThemeChange = (themeId: string) => {
    if (user) {
      setUser({
        ...user,
        preferences: {
          ...user.preferences,
          theme: themeId as 'light' | 'dark' | 'auto' | 'calm' | 'minimal' | 'gamer' | 'neon',
        }
      });
    }
  };

  const selectedTheme = themes.find(theme => theme.id === currentTheme) || themes[0];

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Palette className="w-6 h-6 text-primary-500" />
        <h3 className="text-lg font-semibold text-gray-900">Theme</h3>
      </div>

      <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
        {themes.map((theme) => {
          const Icon = theme.icon;
          const isSelected = currentTheme === theme.id;
          
          return (
            <button
              key={theme.id}
              onClick={() => handleThemeChange(theme.id)}
              className={`p-4 rounded-lg border-2 transition-all text-left ${
                isSelected
                  ? 'border-primary-500 bg-primary-50'
                  : 'border-gray-200 hover:border-gray-300 hover:bg-gray-50'
              }`}
            >
              <div className="flex items-center space-x-3 mb-2">
                <Icon className={`w-5 h-5 ${isSelected ? 'text-primary-600' : 'text-gray-600'}`} />
                <div>
                  <div className={`font-medium ${isSelected ? 'text-primary-900' : 'text-gray-900'}`}>
                    {theme.name}
                  </div>
                  <div className={`text-sm ${isSelected ? 'text-primary-700' : 'text-gray-600'}`}>
                    {theme.description}
                  </div>
                </div>
              </div>
              
              {/* Theme Preview */}
              <div className={`h-8 rounded-lg bg-gradient-to-r ${theme.colors.primary} flex items-center justify-center`}>
                <div className="w-2 h-2 bg-white rounded-full opacity-80"></div>
              </div>
            </button>
          );
        })}
      </div>

      {/* Theme Preview */}
      <div className="mt-6 p-4 bg-gray-50 rounded-lg">
        <h4 className="text-sm font-medium text-gray-700 mb-3">Preview</h4>
        <div className={`p-4 rounded-lg bg-gradient-to-r ${selectedTheme.colors.primary} text-white`}>
          <div className="flex items-center space-x-2 mb-2">
            <div className="w-3 h-3 bg-white rounded-full"></div>
            <span className="font-medium">Sample Block</span>
          </div>
          <div className="text-sm opacity-90">This is how your blocks will look</div>
        </div>
      </div>
    </div>
  );
};

export default ThemeSelector;
