import React from 'react';
import { DndContext, DragEndEvent, DragOverlay, DragStartEvent } from '@dnd-kit/core';
import { SortableContext, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { useStore } from '../store/useStore';
import { TimeBlock } from '../types';
import TowerBlock from './TowerBlock';
import { format } from 'date-fns';
import { Plus, Target } from 'lucide-react';

const DailyTower: React.FC = () => {
  const { 
    currentTower, 
    blocks, 
    selectedDate, 
    moveBlock, 
    addBlock,
    completeBlock 
  } = useStore();

  const todayBlocks = blocks.filter(block => 
    block.scheduledFor && 
    format(block.scheduledFor, 'yyyy-MM-dd') === format(selectedDate, 'yyyy-MM-dd')
  );

  const handleDragStart = (event: DragStartEvent) => {
    // Handle drag start if needed
  };

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    
    if (over && active.id !== over.id) {
      const blockId = active.id as string;
      const newTowerId = over.id as string;
      
      // Move block to new position
      moveBlock(blockId, newTowerId, 0);
    }
  };

  const handleAddBlock = () => {
    const newBlock: TimeBlock = {
      id: Date.now().toString(),
      title: 'New Block',
      description: 'Click to edit',
      duration: 25,
      category: 'work',
      priority: 'medium',
      energyLevel: 'medium',
      completed: false,
      createdAt: new Date(),
      scheduledFor: selectedDate,
      tags: [],
    };
    
    addBlock(newBlock);
  };

  const handleCompleteBlock = (blockId: string) => {
    completeBlock(blockId);
  };

  const totalDuration = todayBlocks.reduce((sum, block) => sum + block.duration, 0);
  const completedDuration = todayBlocks
    .filter(block => block.completed)
    .reduce((sum, block) => sum + block.duration, 0);

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">
            {format(selectedDate, 'EEEE, MMMM do')}
          </h2>
          <p className="text-gray-600">
            {completedDuration} / {totalDuration} minutes completed
          </p>
        </div>
        
        <button
          onClick={handleAddBlock}
          className="flex items-center space-x-2 px-4 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors"
        >
          <Plus className="w-5 h-5" />
          <span>Add Block</span>
        </button>
      </div>

      {/* Progress Bar */}
      <div className="mb-6">
        <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
          <span>Daily Progress</span>
          <span>{Math.round((completedDuration / totalDuration) * 100) || 0}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-3">
          <div 
            className="bg-gradient-to-r from-primary-500 to-primary-600 h-3 rounded-full transition-all duration-500"
            style={{ width: `${(completedDuration / totalDuration) * 100 || 0}%` }}
          />
        </div>
      </div>

      {/* Tower Container */}
      <DndContext onDragStart={handleDragStart} onDragEnd={handleDragEnd}>
        <div className="min-h-[400px] flex flex-col items-center">
          {todayBlocks.length === 0 ? (
            <div className="flex flex-col items-center justify-center h-64 text-gray-500">
              <Target className="w-16 h-16 mb-4 opacity-50" />
              <h3 className="text-xl font-medium mb-2">No blocks today</h3>
              <p className="text-center max-w-sm">
                Add some blocks to start building your daily tower. 
                Each block represents a task or focus session.
              </p>
            </div>
          ) : (
            <SortableContext items={todayBlocks.map(block => block.id)} strategy={verticalListSortingStrategy}>
              <div className="w-full max-w-sm space-y-2">
                {todayBlocks.map((block, index) => (
                  <TowerBlock
                    key={block.id}
                    block={block}
                    index={index}
                    onComplete={() => handleCompleteBlock(block.id)}
                  />
                ))}
              </div>
            </SortableContext>
          )}
        </div>
      </DndContext>

      {/* Tower Completion Animation */}
      {todayBlocks.length > 0 && todayBlocks.every(block => block.completed) && (
        <div className="mt-6 text-center">
          <div className="inline-flex items-center space-x-2 px-6 py-3 bg-gradient-to-r from-green-500 to-green-600 text-white rounded-full animate-pulse">
            <Target className="w-5 h-5" />
            <span className="font-medium">Tower Complete! 🎉</span>
          </div>
        </div>
      )}
    </div>
  );
};

export default DailyTower;
