import React, { useState } from 'react';
import { useSortable } from '@dnd-kit/sortable';
import { CSS } from '@dnd-kit/utilities';
import { TimeBlock, BlockCategory } from '../types';
import { 
  Clock, 
  CheckCircle2, 
  Play, 
  MoreVertical, 
  Edit3,
  Trash2,
  GripVertical
} from 'lucide-react';

interface TowerBlockProps {
  block: TimeBlock;
  index: number;
  onComplete: () => void;
}

const categoryColors: Record<BlockCategory, string> = {
  work: 'category-work',
  study: 'category-study',
  exercise: 'category-exercise',
  break: 'category-break',
  personal: 'category-personal',
  habit: 'category-habit',
  meeting: 'category-meeting',
  creative: 'category-creative',
};

const priorityColors = {
  low: 'border-l-green-400',
  medium: 'border-l-yellow-400',
  high: 'border-l-orange-400',
  urgent: 'border-l-red-400',
};

const TowerBlock: React.FC<TowerBlockProps> = ({ block, index, onComplete }) => {
  const [isEditing, setIsEditing] = useState(false);
  const [showMenu, setShowMenu] = useState(false);
  
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: block.id });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
  };

  const formatDuration = (minutes: number) => {
    if (minutes < 60) {
      return `${minutes}m`;
    }
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    return mins > 0 ? `${hours}h ${mins}m` : `${hours}h`;
  };

  const getEnergyColor = (level: string) => {
    switch (level) {
      case 'high': return 'energy-high';
      case 'medium': return 'energy-medium';
      case 'low': return 'energy-low';
      default: return 'energy-medium';
    }
  };

  return (
    <div
      ref={setNodeRef}
      style={style}
      className={`
        relative group bg-white rounded-lg shadow-md border-l-4 transition-all duration-200
        ${priorityColors[block.priority]}
        ${categoryColors[block.category]}
        ${block.completed ? 'opacity-75 scale-95' : 'hover:shadow-lg hover:scale-102'}
        ${isDragging ? 'opacity-50 scale-105' : ''}
        ${block.completed ? 'completion-glow' : ''}
      `}
    >
      {/* Drag Handle */}
      <div
        {...attributes}
        {...listeners}
        className="absolute left-2 top-1/2 transform -translate-y-1/2 opacity-0 group-hover:opacity-100 transition-opacity cursor-grab active:cursor-grabbing"
      >
        <GripVertical className="w-4 h-4 text-gray-400" />
      </div>

      <div className="p-4 pl-8">
        {/* Header */}
        <div className="flex items-start justify-between mb-2">
          <div className="flex-1">
            {isEditing ? (
              <input
                type="text"
                defaultValue={block.title}
                className="w-full text-lg font-semibold bg-transparent border-b border-gray-300 focus:border-primary-500 focus:outline-none"
                onBlur={() => setIsEditing(false)}
                onKeyPress={(e) => {
                  if (e.key === 'Enter') {
                    setIsEditing(false);
                  }
                }}
                autoFocus
              />
            ) : (
              <h3 
                className="text-lg font-semibold text-gray-900 cursor-pointer hover:text-primary-600"
                onClick={() => setIsEditing(true)}
              >
                {block.title}
              </h3>
            )}
            
            {block.description && (
              <p className="text-sm text-gray-600 mt-1">{block.description}</p>
            )}
          </div>

          {/* Actions Menu */}
          <div className="relative">
            <button
              onClick={() => setShowMenu(!showMenu)}
              className="p-1 text-gray-400 hover:text-gray-600 transition-colors"
            >
              <MoreVertical className="w-4 h-4" />
            </button>
            
            {showMenu && (
              <div className="absolute right-0 top-8 bg-white rounded-lg shadow-lg border py-1 z-10 min-w-[120px]">
                <button
                  onClick={() => {
                    setIsEditing(true);
                    setShowMenu(false);
                  }}
                  className="w-full px-3 py-2 text-left text-sm text-gray-700 hover:bg-gray-100 flex items-center space-x-2"
                >
                  <Edit3 className="w-4 h-4" />
                  <span>Edit</span>
                </button>
                <button
                  onClick={() => {
                    // Handle delete
                    setShowMenu(false);
                  }}
                  className="w-full px-3 py-2 text-left text-sm text-red-600 hover:bg-red-50 flex items-center space-x-2"
                >
                  <Trash2 className="w-4 h-4" />
                  <span>Delete</span>
                </button>
              </div>
            )}
          </div>
        </div>

        {/* Block Info */}
        <div className="flex items-center justify-between text-sm text-gray-600">
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-1">
              <Clock className="w-4 h-4" />
              <span>{formatDuration(block.duration)}</span>
            </div>
            
            <div className={`px-2 py-1 rounded-full text-xs font-medium ${getEnergyColor(block.energyLevel)} text-white`}>
              {block.energyLevel} energy
            </div>
          </div>

          {/* Complete Button */}
          {!block.completed ? (
            <button
              onClick={onComplete}
              className="flex items-center space-x-1 px-3 py-1 bg-green-500 text-white rounded-full hover:bg-green-600 transition-colors text-sm"
            >
              <Play className="w-3 h-3" />
              <span>Start</span>
            </button>
          ) : (
            <div className="flex items-center space-x-1 text-green-600">
              <CheckCircle2 className="w-4 h-4" />
              <span className="text-sm font-medium">Completed</span>
            </div>
          )}
        </div>

        {/* Tags */}
        {block.tags.length > 0 && (
          <div className="flex flex-wrap gap-1 mt-2">
            {block.tags.map((tag, tagIndex) => (
              <span
                key={tagIndex}
                className="px-2 py-1 bg-gray-100 text-gray-600 text-xs rounded-full"
              >
                {tag}
              </span>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default TowerBlock;
