import React, { useState } from 'react';
import { Bot, Sparkles, Clock, Zap, Calendar, Send } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock, BlockCategory, Priority, EnergyLevel } from '../types';

const AIAssistant: React.FC = () => {
  const { addBlock, selectedDate, logEnergyLevel } = useStore();
  const [input, setInput] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);
  const [suggestions, setSuggestions] = useState<TimeBlock[]>([]);

  const parseNaturalLanguage = (text: string): TimeBlock[] => {
    const blocks: TimeBlock[] = [];
    const now = new Date();
    
    // Simple pattern matching for common phrases
    const patterns = [
      // Time + Activity patterns
      { 
        regex: /(\d+)\s*(?:hours?|hrs?|h)\s*(?:of\s*)?(.+)/i, 
        extract: (match: RegExpMatchArray) => ({
          duration: parseInt(match[1]) * 60,
          title: match[2].trim(),
          category: 'work' as BlockCategory
        })
      },
      { 
        regex: /(\d+)\s*(?:minutes?|mins?|m)\s*(?:of\s*)?(.+)/i, 
        extract: (match: RegExpMatchArray) => ({
          duration: parseInt(match[1]),
          title: match[2].trim(),
          category: 'work' as BlockCategory
        })
      },
      // Activity + Time patterns
      { 
        regex: /(.+?)\s+(?:for\s+)?(\d+)\s*(?:hours?|hrs?|h)/i, 
        extract: (match: RegExpMatchArray) => ({
          duration: parseInt(match[2]) * 60,
          title: match[1].trim(),
          category: 'work' as BlockCategory
        })
      },
      { 
        regex: /(.+?)\s+(?:for\s+)?(\d+)\s*(?:minutes?|mins?|m)/i, 
        extract: (match: RegExpMatchArray) => ({
          duration: parseInt(match[2]),
          title: match[1].trim(),
          category: 'work' as BlockCategory
        })
      },
      // Specific activities
      { 
        regex: /(?:gym|workout|exercise)/i, 
        extract: () => ({
          duration: 60,
          title: 'Gym Session',
          category: 'exercise' as BlockCategory
        })
      },
      { 
        regex: /(?:study|studying|learn)/i, 
        extract: () => ({
          duration: 45,
          title: 'Study Session',
          category: 'study' as BlockCategory
        })
      },
      { 
        regex: /(?:break|coffee|rest)/i, 
        extract: () => ({
          duration: 15,
          title: 'Break',
          category: 'break' as BlockCategory
        })
      },
      { 
        regex: /(?:meeting|call|call\s+with)/i, 
        extract: () => ({
          duration: 30,
          title: 'Meeting',
          category: 'meeting' as BlockCategory
        })
      }
    ];

    // Split by common separators
    const phrases = text.split(/[,\n;]/).map(p => p.trim()).filter(p => p.length > 0);
    
    phrases.forEach(phrase => {
      for (const pattern of patterns) {
        const match = phrase.match(pattern.regex);
        if (match) {
          const extracted = pattern.extract(match);
          
          // Determine priority and energy level based on keywords
          let priority: Priority = 'medium';
          let energyLevel: EnergyLevel = 'medium';
          
          if (phrase.toLowerCase().includes('urgent') || phrase.toLowerCase().includes('important')) {
            priority = 'high';
          }
          if (phrase.toLowerCase().includes('low') || phrase.toLowerCase().includes('easy')) {
            priority = 'low';
            energyLevel = 'low';
          }
          if (phrase.toLowerCase().includes('deep') || phrase.toLowerCase().includes('focus')) {
            energyLevel = 'high';
            priority = 'high';
          }

          const block: TimeBlock = {
            id: Date.now().toString() + Math.random(),
            title: extracted.title,
            description: `AI generated from: "${phrase}"`,
            duration: extracted.duration,
            category: extracted.category,
            priority,
            energyLevel,
            completed: false,
            createdAt: now,
            scheduledFor: selectedDate,
            tags: ['ai-generated'],
          };
          
          blocks.push(block);
          break; // Only match first pattern for each phrase
        }
      }
    });

    return blocks;
  };

  const handleSubmit = async () => {
    if (!input.trim()) return;
    
    setIsProcessing(true);
    
    // Simulate AI processing delay
    await new Promise(resolve => setTimeout(resolve, 1000));
    
    const generatedBlocks = parseNaturalLanguage(input);
    setSuggestions(generatedBlocks);
    setIsProcessing(false);
  };

  const handleAcceptSuggestion = (block: TimeBlock) => {
    addBlock(block);
    setSuggestions(prev => prev.filter(b => b.id !== block.id));
  };

  const handleAcceptAll = () => {
    suggestions.forEach(block => addBlock(block));
    setSuggestions([]);
    setInput('');
  };

  const examplePrompts = [
    "3 hours of coding, gym at 6pm, call mom tomorrow",
    "2 hours deep work, 30 min break, 1 hour study",
    "Meeting with team, workout, coffee break",
    "Focus session for 90 minutes, then exercise",
  ];

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Bot className="w-6 h-6 text-purple-500" />
        <h3 className="text-lg font-semibold text-gray-900">AI Assistant</h3>
        <div className="flex items-center space-x-1 px-2 py-1 bg-purple-100 text-purple-700 rounded-full text-xs font-medium">
          <Sparkles className="w-3 h-3" />
          <span>Beta</span>
        </div>
      </div>

      {/* Input Area */}
      <div className="mb-6">
        <label className="block text-sm font-medium text-gray-700 mb-2">
          Describe your day in natural language
        </label>
        <div className="flex space-x-2">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSubmit()}
            placeholder="e.g., '3 hours of coding, gym at 6pm, call mom tomorrow'"
            className="flex-1 px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
            disabled={isProcessing}
          />
          <button
            onClick={handleSubmit}
            disabled={!input.trim() || isProcessing}
            className="px-6 py-3 bg-purple-500 text-white rounded-lg hover:bg-purple-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center space-x-2"
          >
            {isProcessing ? (
              <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin" />
            ) : (
              <Send className="w-5 h-5" />
            )}
          </button>
        </div>
      </div>

      {/* Example Prompts */}
      <div className="mb-6">
        <p className="text-sm text-gray-600 mb-2">Try these examples:</p>
        <div className="flex flex-wrap gap-2">
          {examplePrompts.map((prompt, index) => (
            <button
              key={index}
              onClick={() => setInput(prompt)}
              className="px-3 py-1 bg-gray-100 text-gray-700 rounded-full text-sm hover:bg-gray-200 transition-colors"
            >
              {prompt}
            </button>
          ))}
        </div>
      </div>

      {/* Suggestions */}
      {suggestions.length > 0 && (
        <div className="space-y-4">
          <div className="flex items-center justify-between">
            <h4 className="font-medium text-gray-900">AI Suggestions</h4>
            <button
              onClick={handleAcceptAll}
              className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors text-sm"
            >
              Accept All
            </button>
          </div>
          
          <div className="space-y-3">
            {suggestions.map((block) => (
              <div
                key={block.id}
                className="p-4 border border-gray-200 rounded-lg hover:border-purple-300 transition-colors"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <h5 className="font-medium text-gray-900">{block.title}</h5>
                    <p className="text-sm text-gray-600 mb-2">{block.description}</p>
                    
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <div className="flex items-center space-x-1">
                        <Clock className="w-4 h-4" />
                        <span>{block.duration}m</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Zap className="w-4 h-4" />
                        <span className="capitalize">{block.energyLevel} energy</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Calendar className="w-4 h-4" />
                        <span className="capitalize">{block.category}</span>
                      </div>
                    </div>
                  </div>
                  
                  <button
                    onClick={() => handleAcceptSuggestion(block)}
                    className="ml-4 px-4 py-2 bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors text-sm"
                  >
                    Add Block
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* AI Tips */}
      <div className="mt-6 p-4 bg-purple-50 rounded-lg">
        <h4 className="font-medium text-purple-900 mb-2">AI Tips</h4>
        <ul className="text-sm text-purple-800 space-y-1">
          <li>• Include time durations: "2 hours", "30 minutes", "1h"</li>
          <li>• Mention activities: "coding", "gym", "study", "meeting"</li>
          <li>• Use keywords like "urgent", "deep work", "break"</li>
          <li>• Separate multiple tasks with commas or new lines</li>
        </ul>
      </div>
    </div>
  );
};

export default AIAssistant;
