import React, { useState, useEffect } from 'react';
import { Users, Share2, Video, MessageCircle, UserPlus, Crown, Clock, Zap } from 'lucide-react';
import { useStore } from '../store/useStore';
import { TimeBlock } from '../types';

interface FocusRoom {
  id: string;
  name: string;
  host: string;
  participants: number;
  maxParticipants: number;
  duration: number;
  category: string;
  isActive: boolean;
  startTime: Date;
  description: string;
}

interface SharedTower {
  id: string;
  name: string;
  owner: string;
  blocks: TimeBlock[];
  sharedWith: string[];
  isPublic: boolean;
  createdAt: Date;
  likes: number;
  comments: number;
}

interface AccountabilityPartner {
  id: string;
  name: string;
  avatar: string;
  status: 'online' | 'busy' | 'away' | 'offline';
  currentBlock?: string;
  streak: number;
  lastActive: Date;
}

const SocialFeatures: React.FC = () => {
  const { blocks } = useStore();
  const [activeTab, setActiveTab] = useState<'rooms' | 'towers' | 'partners'>('rooms');
  const [isInRoom, setIsInRoom] = useState(false);
  const [currentRoom, setCurrentRoom] = useState<FocusRoom | null>(null);

  // Mock focus rooms data
  const [focusRooms, setFocusRooms] = useState<FocusRoom[]>([
    {
      id: '1',
      name: 'Deep Work Session',
      host: 'FocusMaster',
      participants: 8,
      maxParticipants: 12,
      duration: 90,
      category: 'work',
      isActive: true,
      startTime: new Date(Date.now() - 15 * 60 * 1000),
      description: 'Focused work session for productivity enthusiasts'
    },
    {
      id: '2',
      name: 'Study Group',
      host: 'StudyBuddy',
      participants: 5,
      maxParticipants: 8,
      duration: 60,
      category: 'study',
      isActive: true,
      startTime: new Date(Date.now() - 5 * 60 * 1000),
      description: 'Group study session for exam preparation'
    },
    {
      id: '3',
      name: 'Creative Flow',
      host: 'CreativeSoul',
      participants: 3,
      maxParticipants: 6,
      duration: 45,
      category: 'creative',
      isActive: false,
      startTime: new Date(Date.now() + 30 * 60 * 1000),
      description: 'Creative work session for artists and designers'
    }
  ]);

  // Mock shared towers data
  const [sharedTowers, setSharedTowers] = useState<SharedTower[]>([
    {
      id: '1',
      name: 'Productive Monday',
      owner: 'ProductivityPro',
      blocks: blocks.slice(0, 5),
      sharedWith: ['user1', 'user2', 'user3'],
      isPublic: true,
      createdAt: new Date(Date.now() - 2 * 60 * 60 * 1000),
      likes: 12,
      comments: 3
    },
    {
      id: '2',
      name: 'Study Schedule',
      owner: 'StudyBuddy',
      blocks: blocks.slice(0, 3),
      sharedWith: ['user4', 'user5'],
      isPublic: false,
      createdAt: new Date(Date.now() - 4 * 60 * 60 * 1000),
      likes: 8,
      comments: 1
    }
  ]);

  // Mock accountability partners data
  const [partners, setPartners] = useState<AccountabilityPartner[]>([
    {
      id: '1',
      name: 'FocusBuddy',
      avatar: '👨‍💻',
      status: 'online',
      currentBlock: 'Deep Work Session',
      streak: 15,
      lastActive: new Date(Date.now() - 5 * 60 * 1000)
    },
    {
      id: '2',
      name: 'StudyPartner',
      avatar: '👩‍🎓',
      status: 'busy',
      currentBlock: 'Exam Prep',
      streak: 8,
      lastActive: new Date(Date.now() - 2 * 60 * 1000)
    },
    {
      id: '3',
      name: 'WorkoutPal',
      avatar: '💪',
      status: 'away',
      streak: 22,
      lastActive: new Date(Date.now() - 30 * 60 * 1000)
    }
  ]);

  const joinRoom = (room: FocusRoom) => {
    if (room.participants < room.maxParticipants) {
      setCurrentRoom(room);
      setIsInRoom(true);
      setFocusRooms(focusRooms.map(r => 
        r.id === room.id ? { ...r, participants: r.participants + 1 } : r
      ));
    }
  };

  const leaveRoom = () => {
    if (currentRoom) {
      setFocusRooms(focusRooms.map(r => 
        r.id === currentRoom.id ? { ...r, participants: r.participants - 1 } : r
      ));
    }
    setCurrentRoom(null);
    setIsInRoom(false);
  };

  const shareTower = (tower: SharedTower) => {
    // In real app, this would open sharing dialog
    console.log('Sharing tower:', tower.name);
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'online': return 'text-green-600 bg-green-100';
      case 'busy': return 'text-red-600 bg-red-100';
      case 'away': return 'text-yellow-600 bg-yellow-100';
      case 'offline': return 'text-gray-600 bg-gray-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'work': return '💼';
      case 'study': return '📚';
      case 'creative': return '🎨';
      case 'exercise': return '💪';
      case 'personal': return '🏠';
      default: return '📋';
    }
  };

  return (
    <div className="bg-white rounded-xl shadow-lg p-6">
      <div className="flex items-center space-x-2 mb-6">
        <Users className="w-6 h-6 text-blue-500" />
        <h3 className="text-lg font-semibold text-gray-900">Social Features</h3>
      </div>

      {/* Tab Navigation */}
      <div className="flex space-x-1 mb-6 bg-gray-100 rounded-lg p-1">
        {[
          { id: 'rooms', label: 'Focus Rooms', icon: Video },
          { id: 'towers', label: 'Shared Towers', icon: Share2 },
          { id: 'partners', label: 'Partners', icon: UserPlus },
        ].map((tab) => {
          const Icon = tab.icon;
          return (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id as any)}
              className={`flex-1 flex items-center justify-center space-x-2 px-3 py-2 rounded-md transition-colors ${
                activeTab === tab.id
                  ? 'bg-white text-blue-600 shadow-sm'
                  : 'text-gray-600 hover:text-gray-900'
              }`}
            >
              <Icon className="w-4 h-4" />
              <span className="text-sm font-medium">{tab.label}</span>
            </button>
          );
        })}
      </div>

      {/* Current Room Status */}
      {isInRoom && currentRoom && (
        <div className="mb-6 p-4 bg-gradient-to-r from-green-50 to-blue-50 rounded-lg border border-green-200">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="w-3 h-3 bg-green-500 rounded-full animate-pulse"></div>
              <div>
                <h4 className="font-medium text-gray-900">In Focus Room: {currentRoom.name}</h4>
                <p className="text-sm text-gray-600">
                  {currentRoom.participants} participants • {currentRoom.duration} minutes
                </p>
              </div>
            </div>
            <button
              onClick={leaveRoom}
              className="px-4 py-2 bg-red-500 text-white rounded-lg hover:bg-red-600 transition-colors text-sm"
            >
              Leave Room
            </button>
          </div>
        </div>
      )}

      {/* Tab Content */}
      <div className="space-y-4">
        {activeTab === 'rooms' && (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h4 className="font-medium text-gray-900">Available Focus Rooms</h4>
              <button className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm">
                Create Room
              </button>
            </div>
            
            {focusRooms.map((room) => (
              <div
                key={room.id}
                className={`p-4 rounded-lg border-2 ${
                  room.isActive 
                    ? 'border-green-200 bg-green-50' 
                    : 'border-gray-200 bg-gray-50'
                }`}
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-2">
                      <span className="text-2xl">{getCategoryIcon(room.category)}</span>
                      <div>
                        <h5 className="font-medium text-gray-900">{room.name}</h5>
                        <p className="text-sm text-gray-600">Hosted by {room.host}</p>
                      </div>
                    </div>
                    
                    <p className="text-sm text-gray-600 mb-3">{room.description}</p>
                    
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <div className="flex items-center space-x-1">
                        <Users className="w-4 h-4" />
                        <span>{room.participants}/{room.maxParticipants}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Clock className="w-4 h-4" />
                        <span>{room.duration} min</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Zap className="w-4 h-4" />
                        <span className={room.isActive ? 'text-green-600' : 'text-gray-500'}>
                          {room.isActive ? 'Active' : 'Starting Soon'}
                        </span>
                      </div>
                    </div>
                  </div>
                  
                  <button
                    onClick={() => joinRoom(room)}
                    disabled={room.participants >= room.maxParticipants || !room.isActive}
                    className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors text-sm"
                  >
                    {room.participants >= room.maxParticipants ? 'Full' : 'Join'}
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'towers' && (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h4 className="font-medium text-gray-900">Shared Towers</h4>
              <button className="px-4 py-2 bg-green-500 text-white rounded-lg hover:bg-green-600 transition-colors text-sm">
                Share My Tower
              </button>
            </div>
            
            {sharedTowers.map((tower) => (
              <div
                key={tower.id}
                className="p-4 rounded-lg border border-gray-200 hover:border-blue-300 transition-colors"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-2">
                      <h5 className="font-medium text-gray-900">{tower.name}</h5>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                        tower.isPublic ? 'text-green-600 bg-green-100' : 'text-blue-600 bg-blue-100'
                      }`}>
                        {tower.isPublic ? 'Public' : 'Private'}
                      </span>
                    </div>
                    
                    <p className="text-sm text-gray-600 mb-3">
                      by {tower.owner} • {tower.blocks.length} blocks
                    </p>
                    
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <div className="flex items-center space-x-1">
                        <span>❤️</span>
                        <span>{tower.likes}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <MessageCircle className="w-4 h-4" />
                        <span>{tower.comments}</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Users className="w-4 h-4" />
                        <span>{tower.sharedWith.length} shared</span>
                      </div>
                    </div>
                  </div>
                  
                  <button
                    onClick={() => shareTower(tower)}
                    className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors text-sm"
                  >
                    View
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}

        {activeTab === 'partners' && (
          <div className="space-y-4">
            <div className="flex items-center justify-between">
              <h4 className="font-medium text-gray-900">Accountability Partners</h4>
              <button className="px-4 py-2 bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors text-sm">
                Find Partners
              </button>
            </div>
            
            {partners.map((partner) => (
              <div
                key={partner.id}
                className="p-4 rounded-lg border border-gray-200 hover:border-purple-300 transition-colors"
              >
                <div className="flex items-center space-x-3">
                  <div className="text-3xl">{partner.avatar}</div>
                  
                  <div className="flex-1">
                    <div className="flex items-center space-x-2 mb-1">
                      <h5 className="font-medium text-gray-900">{partner.name}</h5>
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(partner.status)}`}>
                        {partner.status}
                      </span>
                    </div>
                    
                    {partner.currentBlock && (
                      <p className="text-sm text-gray-600 mb-1">
                        Currently: {partner.currentBlock}
                      </p>
                    )}
                    
                    <div className="flex items-center space-x-4 text-sm text-gray-500">
                      <div className="flex items-center space-x-1">
                        <Crown className="w-4 h-4" />
                        <span>{partner.streak} day streak</span>
                      </div>
                      <div className="flex items-center space-x-1">
                        <Clock className="w-4 h-4" />
                        <span>Active {Math.round((Date.now() - partner.lastActive.getTime()) / (1000 * 60))}m ago</span>
                      </div>
                    </div>
                  </div>
                  
                  <button className="px-4 py-2 bg-purple-500 text-white rounded-lg hover:bg-purple-600 transition-colors text-sm">
                    Message
                  </button>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Social Stats */}
      <div className="mt-6 p-4 bg-gradient-to-r from-blue-50 to-purple-50 rounded-lg">
        <h4 className="font-medium text-gray-900 mb-2">Your Social Stats</h4>
        <div className="grid grid-cols-3 gap-4 text-center">
          <div>
            <div className="text-2xl font-bold text-blue-600">12</div>
            <div className="text-sm text-gray-600">Focus Sessions</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-green-600">8</div>
            <div className="text-sm text-gray-600">Towers Shared</div>
          </div>
          <div>
            <div className="text-2xl font-bold text-purple-600">3</div>
            <div className="text-sm text-gray-600">Partners</div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SocialFeatures;
