import React from 'react';
import { Trophy, Target, Clock, Flame, TrendingUp, Award } from 'lucide-react';
import { useStore } from '../store/useStore';

const StatsPanel: React.FC = () => {
  const { user, currentTower, blocks } = useStore();

  if (!user) return null;

  const todayBlocks = blocks.filter(block => 
    block.scheduledFor && 
    new Date(block.scheduledFor).toDateString() === new Date().toDateString()
  );

  const completedToday = todayBlocks.filter(block => block.completed).length;
  const totalToday = todayBlocks.length;
  const focusTimeToday = todayBlocks
    .filter(block => block.completed)
    .reduce((sum, block) => sum + block.duration, 0);

  const stats = [
    {
      icon: Trophy,
      label: 'Level',
      value: user.stats.level,
      color: 'text-yellow-600',
      bgColor: 'bg-yellow-100',
    },
    {
      icon: Flame,
      label: 'Streak',
      value: user.stats.currentStreak,
      color: 'text-orange-600',
      bgColor: 'bg-orange-100',
    },
    {
      icon: Target,
      label: 'Today',
      value: `${completedToday}/${totalToday}`,
      color: 'text-green-600',
      bgColor: 'bg-green-100',
    },
    {
      icon: Clock,
      label: 'Focus Time',
      value: `${focusTimeToday}m`,
      color: 'text-blue-600',
      bgColor: 'bg-blue-100',
    },
  ];

  const achievements = [
    {
      title: 'First Block',
      description: 'Complete your first focus block',
      unlocked: user.stats.totalBlocksCompleted > 0,
      icon: '🎯',
    },
    {
      title: 'Streak Master',
      description: 'Maintain a 7-day streak',
      unlocked: user.stats.currentStreak >= 7,
      icon: '🔥',
    },
    {
      title: 'Focus Warrior',
      description: 'Complete 100 focus blocks',
      unlocked: user.stats.totalBlocksCompleted >= 100,
      icon: '⚔️',
    },
    {
      title: 'Time Master',
      description: 'Log 1000 minutes of focus time',
      unlocked: user.stats.focusTime >= 1000,
      icon: '⏰',
    },
  ];

  return (
    <div className="space-y-6">
      {/* Main Stats */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Today's Progress</h3>
        
        <div className="grid grid-cols-2 gap-4">
          {stats.map((stat, index) => (
            <div key={index} className={`p-4 rounded-lg ${stat.bgColor}`}>
              <div className="flex items-center space-x-3">
                <stat.icon className={`w-6 h-6 ${stat.color}`} />
                <div>
                  <div className={`text-2xl font-bold ${stat.color}`}>
                    {stat.value}
                  </div>
                  <div className="text-sm text-gray-600">
                    {stat.label}
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>

        {/* Progress Bar */}
        <div className="mt-6">
          <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
            <span>Daily Goal</span>
            <span>{Math.round((completedToday / Math.max(totalToday, 1)) * 100)}%</span>
          </div>
          <div className="w-full bg-gray-200 rounded-full h-2">
            <div 
              className="bg-gradient-to-r from-primary-500 to-primary-600 h-2 rounded-full transition-all duration-500"
              style={{ width: `${(completedToday / Math.max(totalToday, 1)) * 100}%` }}
            />
          </div>
        </div>
      </div>

      {/* Achievements */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex items-center space-x-2 mb-4">
          <Award className="w-6 h-6 text-purple-500" />
          <h3 className="text-lg font-semibold text-gray-900">Achievements</h3>
        </div>
        
        <div className="space-y-3">
          {achievements.map((achievement, index) => (
            <div
              key={index}
              className={`flex items-center space-x-3 p-3 rounded-lg transition-colors ${
                achievement.unlocked
                  ? 'bg-green-50 border border-green-200'
                  : 'bg-gray-50'
              }`}
            >
              <div className={`text-2xl ${achievement.unlocked ? '' : 'grayscale opacity-50'}`}>
                {achievement.icon}
              </div>
              <div className="flex-1">
                <div className={`font-medium ${
                  achievement.unlocked ? 'text-green-900' : 'text-gray-500'
                }`}>
                  {achievement.title}
                </div>
                <div className={`text-sm ${
                  achievement.unlocked ? 'text-green-700' : 'text-gray-400'
                }`}>
                  {achievement.description}
                </div>
              </div>
              {achievement.unlocked && (
                <div className="text-green-600">
                  <Trophy className="w-5 h-5" />
                </div>
              )}
            </div>
          ))}
        </div>
      </div>

      {/* Weekly Overview */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Weekly Overview</h3>
        
        <div className="space-y-2">
          {['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'].map((day, index) => {
            const dayBlocks = blocks.filter(block => {
              const blockDate = new Date(block.scheduledFor || '');
              return blockDate.getDay() === (index + 1) % 7;
            });
            const completed = dayBlocks.filter(block => block.completed).length;
            const total = dayBlocks.length;
            const percentage = total > 0 ? (completed / total) * 100 : 0;
            
            return (
              <div key={day} className="flex items-center space-x-3">
                <div className="w-8 text-sm font-medium text-gray-600">{day}</div>
                <div className="flex-1 bg-gray-200 rounded-full h-2">
                  <div 
                    className="bg-gradient-to-r from-primary-500 to-primary-600 h-2 rounded-full transition-all duration-500"
                    style={{ width: `${percentage}%` }}
                  />
                </div>
                <div className="w-12 text-sm text-gray-600 text-right">
                  {completed}/{total}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default StatsPanel;
